import { Table } from 'antd';
import '../view/css/biao.css'

const columns = [
    {
      title: '客户名称',
      dataIndex: 'name',
    },
    {
      title: '合同名称',
      dataIndex: 'age',
    },
    {
        title: '合同金额(元)',
        dataIndex: 'age',
      },
      {
        title: '回款金额(元)',
        dataIndex: 'age',
      },
      {
        title: '客户行业',
        dataIndex: 'age',
      },
      {
        title: '客户来源',
        dataIndex: 'age',
      },
      {
        title: '负责人',
        dataIndex: 'age',
      },
    {
      title: '创建人',
      dataIndex: 'address',
    },
    {
        title: '创建时间',
        dataIndex: 'address',
      },
      {
        title: '下单时间',
        dataIndex: 'address',
      },
  ];
  
  const data = [];
  for (let i = 0; i < 15; i++) {
    data.push({
      key: i,
      name: ` ${i}`,
      age: 0,
      address: `0`,
    });
  }

  function Biao4() {
      return(
        <div>
            <Table columns={columns}   scroll={{ y: 370 }} pagination={false} bordered='true'/>
        </div>
      )
  }

export default Biao4