import { Table } from 'antd';
import '../view/css/biao.css'
import axios from 'axios'
import React, { useState, useEffect } from 'react';

  function Sy_17_biao() {
    const [arr,setarr] = useState([])
    useEffect(() => {
      axios.post('http://crm.cimns.com/index.php/bi/customer/portrait',{
        structure_id: 1,
        user_id: '',
        type: 'year',
        type_analyse: 'source'
      }).then((data)=> {
        console.log(data.data.data);
        setarr(data.data.data)
        
      })
    },[])
    
    const columns = [
        {
          title: '客户来源',
          width: 180,
          dataIndex: 'name',
          key: 'name',
          fixed: 'left',
          align:'center'
        },
        { title: '促销活动', dataIndex: 'address1', key: '1',align:'center',width:100 },
        { title: '搜索引擎', dataIndex: 'address2', key: '2',align:'center',width:100 },
        { title: '广告', dataIndex: 'address3', key: '3',align:'center',width:100 },
        { title: '转介绍', dataIndex: 'address4', key: '4',align:'center',width:100 },
        { title: '线上注册', dataIndex: 'address5', key: '5',align:'center',width:100 },
        { title: '线上询价', dataIndex: 'address6', key: '6',align:'center',width:100 },
        { title: '预约上门', dataIndex: 'address7', key: '7',align:'center',width:100 },
        { title: '陌拜', dataIndex: 'address8', key: '8',align:'center',width:100 },
        { title: '招商资源', dataIndex: 'address9', key: '9',align:'center',width:100 },
        { title: '公司资源', dataIndex: 'address10', key: '10',align:'center',width:100 },
        { title: '展会资源', dataIndex: 'address11', key: '11',align:'center',width:100 },
        { title: '个人资源', dataIndex: 'address12', key: '12',align:'center',width:100 },
        { title: '电话咨询', dataIndex: 'address13', key: '13',align:'center',width:100 },
        { title: '邮件咨询', dataIndex: 'address14', key: '14',align:'center',width:100 },
      ];
      
      let data=[]
      if (arr.length === 0) {
        
      }else{
        data = [
          {
            key: '1',
            name: '所有客户(个)',
            address1: arr[0].allCustomer,
            address2:arr[1].allCustomer,
            address3:arr[2].allCustomer,
            address4:arr[3].allCustomer,
            address5:arr[4].allCustomer,
            address6:arr[5].allCustomer,
            address7:arr[6].allCustomer,
            address8:arr[7].allCustomer,
            address9:arr[8].allCustomer,
            address10:arr[9].allCustomer,
            address11:arr[10].allCustomer,
            address12:arr[11].allCustomer,
            address13:arr[12].allCustomer,
            address14:arr[13].allCustomer,
            

          },
          {
            key: '2',
            name: '成交客户(个)',
            address1: arr[0].dealCustomer,
            address2:arr[1].dealCustomer,
            address3:arr[2].dealCustomer,
            address4:arr[3].dealCustomer,
            address5:arr[4].dealCustomer,
            address6:arr[5].dealCustomer,
            address7:arr[6].dealCustomer,
            address8:arr[7].dealCustomer,
            address9:arr[8].dealCustomer,
            address10:arr[9].dealCustomer,
            address11:arr[10].dealCustomer,
            address12:arr[11].dealCustomer,
            address13:arr[12].dealCustomer,
            address14:arr[13].dealCustomer,
          },
        ];
      }
      return(
        <div>
            <Table columns={columns} dataSource={data} scroll={{ x: 1300 }} pagination={false} bordered='true' />
        </div>
      )
  }

export default Sy_17_biao