import React,{Component} from 'react'
import * as echarts from 'echarts';
import axios from 'axios';

class Sy_27_tong extends React.Component{

    componentDidMount() {
        var xm = []
        var sz = []
        axios.post('http://crm.cimns.com/index.php/bi/ranking/recordCustomer',{
            structure_id: 1,
            type: "year",
        }).then((data)=>{
            for (let t = 0; t < 10; t++) {
                xm.push(data.data.data[t].user_name)
            }
            for (let i = 0; i < data.data.data.length; i++) {
                sz.push(data.data.data[i].count)
            }
            var chartDom = document.getElementById('main');
            var myChart = echarts.init(chartDom);
            var option;
            option = {
                tooltip: {
                    trigger: 'axis',
                    width:'100px',
                    axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                        type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                    }
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis: [
                    {
                        name:'(个)',
                        type: 'value'
                    }
                ],
                yAxis: [
                    {
                       
                        type: 'category',
                        axisTick: {
                            show: false
                        },
                        data: xm,
                    }
                ],
                series: [
                    {
                        type: 'bar',
                        barWidth:'60%',
                        marginLeft:'50px',
                        label: {
                            show: true,
                            position: 'inside',
                        },
                        data: sz,
                    },
                ]
            };
                option && myChart.setOption(option);
        }
    )
      

}
        
        render() {
            return(
                <div id="main" style={
                   {width:1038,height:400}
                } ></div>
            )
        }
    }

export default Sy_27_tong