import React, { } from 'react';
import axios from 'axios';
import * as echarts from 'echarts';


class Sy_15_bing extends React.Component{

    componentDidMount() {
        var industry=[]
        var arr=[]
        axios.post('http://crm.cimns.com/index.php/bi/customer/portrait',{
            structure_id: 1,
            user_id: '',
            type: 'year',
            type_analyse: 'industry'
        }).then((data)=>{
            console.log(data.data.data);
            for (let t = 0; t < data.data.data.length; t++) {
                industry.push(data.data.data[t].industry)
            }
            arr=data.data.data
            var chartDom = document.getElementById('main');
            var myChart = echarts.init(chartDom);
            var option;

            var option = {
                title:[
                    {
                        text: '全部客户',
                        left: '220',
                        top:'315'
                    },{
                        text: '成交客户',
                        left: '700',
                        top:'315'
                    },    
                ],
                tooltip: {
                    trigger: 'item',
                },
                
                legend: {
                    data: industry,
                    bottom:'bottom'
                },
                series: [
                    {
                        name: '',
                        type: 'pie',
                        radius: ['30%', '50%'],
                        center: ['25%', '43%'],
                        labelLine: {
                            length: 30,
                        },
                        label: {
                            borderWidth: 1,
                            borderRadius: 4,
                        },
                        data: [
                            {value: arr[0].allCustomer, name: arr[0].industry},
                            {value: arr[1].allCustomer, name: arr[1].industry},
                            {value: arr[2].allCustomer, name: arr[2].industry},
                            {value: arr[3].allCustomer, name: arr[3].industry},
                            {value: arr[4].allCustomer, name: arr[4].industry},
                            {value: arr[5].allCustomer, name: arr[5].industry},
                            {value: arr[6].allCustomer, name: arr[6].industry},
                            {value: arr[7].allCustomer, name: arr[7].industry},
                            {value: arr[8].allCustomer, name: arr[8].industry},
                            {value: arr[9].allCustomer, name: arr[9].industry},
                            {value: arr[10].allCustomer, name: arr[10].industry},
                        ]
                        
                    },{
                        name: '',
                        type: 'pie',
                        radius: ['30%', '50%'],
                        center: ['70%', '43%'],
                        labelLine: {
                            length: 30,
                        },
                        label: {
                            borderWidth: 1,
                            borderRadius: 4,
                        },
                        data: [
                            {value: arr[0].dealCustomer, name: arr[0].industry},
                            {value: arr[1].dealCustomer, name: arr[1].industry},
                            {value: arr[2].dealCustomer, name: arr[2].industry},
                            {value: arr[3].dealCustomer, name: arr[3].industry},
                            {value: arr[4].dealCustomer, name: arr[4].industry},
                            {value: arr[5].dealCustomer, name: arr[5].industry},
                            {value: arr[6].dealCustomer, name: arr[6].industry},
                            {value: arr[7].dealCustomer, name: arr[7].industry},
                            {value: arr[8].dealCustomer, name: arr[8].industry},
                            {value: arr[9].dealCustomer, name: arr[9].industry},
                            {value: arr[10].dealCustomer, name: arr[10].industry},
                        ]
                    },
                ]
       
            };
            
                
            option && myChart.setOption(option);
        })
        
        
    }
        
        render() {
            return(
                <div id="main" style={{width:1032,height:380,}}></div>
            )
        }
    }
export default Sy_15_bing