import { Table } from 'antd';
import '../view/css/biao.css';
import axios from 'axios'
import React, { useState, useEffect } from 'react';
const columns = [
    {
      title: '员工姓名',
      dataIndex: 'name',
      width: 150,
    },
    {
      title: '新增客户数',
      dataIndex: 'aga',
      width: 150,
    },
    {
        title: '成交客户数',
        dataIndex: 'agb',
        width: 150,
      },
];
  

  function Biao2() {
    const [arr,setarr] = useState([])
    useEffect(() => {
      axios.post('http://crm.cimns.com/index.php/bi/customer/statistics',{
        structure_id: 1,
        user_id: '',
        type: 'year'
      }).then((data)=> {
        setarr(data.data.data)
        console.log(data.data.data);
      })
    },[])

    const data = [];
    for (let i = 0; i < arr.length; i++) {
      data.push({
        key: i,
        name:arr[i].realname,
        aga:arr[i].customer_num,
        agb:arr[i].deal_customer_num

      });
    }
      return(
        <div>
            <Table columns={columns} dataSource={data}  scroll={{ y: 370 }} pagination={false} bordered='true'/>
        </div>
      )
  }

export default Biao2