import { Table } from 'antd';
import '../view/css/biao.css'
import axios from 'axios'
import React, { useState, useEffect } from 'react';



  function Sy_13_biao() {
    const columns = [
        {
          title: '日期',
          dataIndex: 'name',
          width: 150,
          align:'center'
        },
        {
          title: '签约合同数(个)',
          dataIndex: 'aga',
          width: 150,
          align:'center'
        },
        {
            title: '签约合同金额(元)',
            dataIndex: 'agb',
            width: 150,
            align:'center'
        },{
            title: '回款金额(元)',
            dataIndex: 'agc',
            width: 150,
            align:'center'
        },
        
      ];
      const [arr,setarr] = useState([])
      useEffect(() => {
        axios.post('http://crm.cimns.com/index.php/bi/contract/summary',{
          structure_id: 1,
          user_id: '',
          type: 'year'
        }).then((data)=> {
          console.log(data.data.data.items);
          setarr(data.data.data.items)
        })
      },[])
      const data = []
      for (let i = 0; i < arr.length; i++) {
        data.push({
          key: i,
          name: arr[i].type,
          aga: arr[i].count,
          agb: arr[i].back,
          agc:arr[i].money
        });
      }
      return(
        <div>
            <Table columns={columns} dataSource={data} pagination={{ pageSize: 50 }} scroll={{ y: 450 }} bordered='true' pagination={false} />
        </div>
      )
  }

export default Sy_13_biao