import React from 'react';
import axios from 'axios';
import * as echarts from 'echarts';


class Sy_31_tong extends React.Component{

    componentDidMount() {
        var rq = []
        var ht = []
        var mb = []
        var wcl = []
        axios.post('http://crm.cimns.com/index.php/bi/achievement/statistics',{
            year: 2021,
            status: 1,
            structure_id: 1,
            user_id: ""
        }).then((data)=>{
            for(let i = 0; i < data.data.data.length;i++){
                rq.push(data.data.data[i].month)
            }
            for(let i = 0; i < data.data.data.length;i++){
                ht.push(data.data.data[i].receivables)
            }
            for(let j = 0; j < data.data.data.length;j++){
                mb.push(data.data.data[j].achiement)
            }
            for(let k = 0; k < data.data.data.length;k++){
                wcl.push(data.data.data[k].rate)
            }
            var chartDom = document.getElementById('main');
            var myChart = echarts.init(chartDom);
            var option;

        option = {
            legend: {
                data: ['合同金额', '目标','完成率'],
                bottom:'bottom'
            },
            xAxis: {
                type: 'category',
                data: rq,
            },
            yAxis: [ {
                        type: 'value',
                        scale: true,
                        max: 1,
                        min: 0,
                        boundaryGap: [0.2, 0.2],
                        axisLabel: {
                            formatter: '{value} 元'
                        }
                    },{
                            
                        type: 'value',
                        scale: true,
                        max: 1,
                        min: 0,
                        boundaryGap: [0.2, 0.2],
                        axisLabel: {
                            formatter: '{value} %'
                        }
                    },],
            series: [{
                name:'合同金额',
                barWidth:'20%',
                data: ht,
                type: 'bar'
            },{
                name:'目标',
                barWidth:'20%',
                data: mb,
                type: 'bar'
            },{
                name: '完成率',
                type: 'line',
                data: wcl,
                   
                }]
        };
            option && myChart.setOption(option);
        },(err)=>{
            console.log(err)
            })
        }
        
        render() {
            return(
                <div id="main" style={
                   {width:1042,height:400}
                }></div>
            )
        }
    }

export default Sy_31_tong