import { Table } from 'antd';
import '../view/css/biao.css'
import axios from 'axios'
import React, { useState, useEffect } from 'react';
const columns = [
    {
      title: '地区',
      dataIndex: 'name',
    },
    {
      title: '成交周期(天)',
      dataIndex: 'age',
    },
    {
        title: '成交客户数',
        dataIndex: 'age',
      },
  ];
  

  function Biao10() {
    const [arr,setarr]=useState([])
    useEffect(()=>{
      axios.post('http://crm.cimns.com/index.php/bi/customer/userCycle',{
        structure_id:1,
        user_id:'',
        type:'year'
      }).then((data)=>{
        console.log(data);
        setarr(data.data.data.users)
      })
    },[])
    const data=[];
    for(let i=0;i<arr.length;i++){
      data.push({
        key:i,
        name:arr[i].realname,
        aga:arr[i].cycle,
        agb:arr[i].customer_num
      })
    }

      return(
        <div>
            <Table columns={columns} dataSource={data}  scroll={{ y: 370 }} pagination={false} bordered='true'/>
        </div>
      )
  }

export default Biao10