import React, { useState, useEffect} from 'react';
import axios from 'axios'
import {Empty,Input, Space,Select, Modal, Button,Pagination} from 'antd';
import './css/kh_3.css'


const { Search } = Input;

  const onSearch = value => console.log(value);
  const { Option } = Select;

  function handleChange(value) {
    console.log(`selected ${value}`);
  }
function Kh_5(){
    const [visible, setVisible] = React.useState(false);
    const [confirmLoading, setConfirmLoading] = React.useState(false);
    const [modalText, setModalText] = React.useState('Content of lie modal');
  
    const showModal = () => {
      setVisible(true);
    };
  
    const handleOk = () => {
      setModalText('lie modal will be closed after two seconds');
      setConfirmLoading(true);
      setTimeout(() => {
        setVisible(false);
        setConfirmLoading(false);
      }, 2000);
    };
  
    const handleCancel = () => {
      console.log('Clicked cancel button');
      setVisible(false);
    };
  
    const [data, setData] = useState([]);
    useEffect(() => {
      const fetchData = async () => {
        const result = await axios.post(
            'http://crm.cimns.com/index.php/crm/contacts/index',{
              page: 1,
              limit: 15,
              search: '',
            }
          )
        console.log(result.data);
        setData(result.data.data.list);
      };
      fetchData(); 
    }, []);
    console.log(data)
   
    return(
        <div className="kh_3">
            <div className="kh_3-header clearfix">
                <div className="kh_3-header-title">
                    <h2>联系人管理</h2>
                </div>
                <div className="kh_3-header-ss">
                    <Space direction="vertical">
                        <Search placeholder="input search text" onSearch={onSearch} style={{ width: 200 }} />
                    </Space>
                </div>
                <div className="kh_3-header-cj clearfix">
                    <div className="kh_3-header-cj-1">
                    <>
                        <Button type="primary" onClick={showModal}>
                            新建联系人
                        </Button>
                        <Modal
                            title="Title"
                            visible={visible}
                            onOk={handleOk}
                            confirmLoading={confirmLoading}
                            onCancel={handleCancel}
                        >
                            <p>{modalText}</p>
                        </Modal>
                    </>
                    </div>
                    <div className="kh_3-header-cj-2">
                        <>
                            <Select defaultValue="gd" style={{ width: 70 }} onChange={handleChange}>
                                <Option value="jack">导入</Option>
                                <Option value="lucy">导出</Option>
                                <Option value="gd">更多</Option>
                            </Select>
                        </>
                    </div>
                </div>
            </div>
            <div className="kh_3-con-top">
                    <div className="kh_3-con-top-1">场景:</div>
                    <div className="kh_3-con-top-2">
                    <>
                        <Select defaultValue="lucy" style={{ width: 120 }} bordered={false}>
                            <Option value="jack">我负责的联系人</Option>
                            <Option value="lucy">全部联系人</Option>
                            <Option value="Yiminghe">下属负责的联系人</Option>
                            
                        </Select>
                    </>
                    </div>
                    <div className="kh_3-con-top-3">
                    <>
                            <Button type="primary" onClick={showModal}>
                                新建联系人
                            </Button>
                            <Modal
                                title="新建联系人"
                                visible={visible}
                                onOk={handleOk}
                                confirmLoading={confirmLoading}
                                onCancel={handleCancel}
                            >
                                <div style={{height:'600px'}}>
                                    <h4>基本信息</h4>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>姓名</h6>
                                        <Input style={{width:'284px',margin:'0 10px'}}/>  
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>客户名称</h6>
                                        <Input style={{width:'284px',margin:'0 10px'}}/>  
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>手机</h6>
                                        <Input style={{width:'284px',margin:'0 10px'}}/>  
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>电话</h6>
                                        <Input style={{width:'284px',margin:'0 10px'}}/>  
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>电子邮箱</h6>
                                        <Input style={{width:'284px',margin:'0 10px'}}/>  
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>是否关键决策人</h6>
                                        <>
                                            <Select defaultValue="请选择" style={{ width: 284 }} onChange={handleChange}>
                                                <Option value="1">是</Option>
                                                <Option value="2">否</Option>
                                                
                                            </Select>
                                        </>
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>职务</h6>
                                        <Input style={{width:'284px',margin:'0 10px'}}/>  
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>性别</h6>
                                        <>
                                            <Select defaultValue="请选择" style={{ width: 280 }} onChange={handleChange}>
                                                <Option value="1">男</Option>
                                                <Option value="2">女</Option>
                
                                            </Select>
                                        </>
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>地址</h6>
                                        <Input style={{width:'284px',margin:'0 10px'}}/>  
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>下次联系时间</h6>
                                        <Input style={{width:'284px',margin:'0 10px'}}/>  
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>备注</h6>
                                        <Input style={{width:'284px',height:'70px',margin:'0 10px'}}/>  
                                    </div>
                                </div>
                            </Modal>
                        </>
                    </div> 
                </div>
            <div className="kh_3-con">
                <div className="kh_3-con-con">
                    <div className="kh_3-con-con-1">
                        <ul>
                                <li>姓名</li>
                                <li>客户名称</li>
                                <li>手机</li>
                                <li>电话</li>
                                <li>电子邮箱</li>
                                <li>是否关键决策人</li>
                                <li>职务</li>
                                <li>性别</li>
                                <li>地址</li>
                                <li>下次执行时间</li>
                                <li>备注</li>
                                <li>创建人</li>
                                <li>更新时间</li>
                                <li>创建时间</li>
                                <li>负责人</li>
                               
                            </ul>
                    </div>
                    <div className="kh_3-con-con-2">
                       
                            {data.map((item)=>( 
                            <ul key={item.create_user_id}>
                                    <li >{item.name}</li>
                                    <li> {item.customer_name}</li>
                                    <li> {item.telephone}</li>
                                    <li> {item.telephone}</li>
                                    <li>  {item.email}</li>
                                    <li>{item.decision}</li>
                                    <li> {item.post}</li>
                                    <li> {item.sex}</li>
                                    <li> {item.customer_name}</li>
                                    <li>{item.next_time}</li>
                                    <li> {item.remark}</li>
                                    <li> {item.create_user_id_info.realname}</li>
                                    <li> {item.update_time}</li>
                                    <li>{item.create_time}</li>
                                    <li> {item.realname}</li>
                                </ul>
                                ))}
                        
                    </div>
                </div>
            </div>
            <div className="kh_3-footer">
                    <>
                        <Pagination size="small" total={50} pagination={false} showSizeChanger showQuickJumper />
                    </>
                </div>
        </div>
    )
}
export default Kh_5