import React, { } from 'react';
import axios from 'axios';
import * as echarts from 'echarts';


class Sy_11_tong extends React.Component{

    componentDidMount() {
        var rq=[]
        var chain_ratio = []
        var lastMonth=[]
        var month=[]
        axios.post('http://crm.cimns.com/index.php/bi/contract/analysis',{
            structure_id: 1,
            user_id: '',
            year: 2021,
            type: 'money',
        }).then((data)=>{
            console.log(data);
            for (let t = 0; t < data.data.data.length; t++) {
                rq.push(data.data.data[t].type)
            }
            for (let t = 0; t < data.data.data.length; t++) {
                chain_ratio.push(data.data.data[t].chain_ratio)
            }
            for (let t = 0; t < data.data.data.length; t++) {
                lastMonth.push(data.data.data[t].lastMonth)
            }
            for (let t = 0; t < data.data.data.length; t++) {
                month.push(data.data.data[t].month)
            }
            var chartDom = document.getElementById('main');
            var myChart = echarts.init(chartDom);
            var option;

            option = {
                xAxis: {
                    type: 'category',
                    data: rq
                },
                yAxis: [
                    {
                        name:'(个)',
                        type: 'value',
                        scale: true,
                        max: 1,
                        min: 0,
                        boundaryGap: [0.2, 0.2],
                    },{
                        type: 'value',
                        scale: true,
                        max: 1,
                        min: 0,
                        boundaryGap: [0.2, 0.2],
                        axisLabel: {
                            formatter: '{value} %'
                        }
                    },
                ],
                series: [{
                    data: chain_ratio,
                    type: 'line',
                    markLine: {
                        data: [
                            {type: 'average', name: '平均值'}
                        ],
                    },
                    markPoint: {
                        data: [
                            {type: 'min', name: '最小值'}
                        ]
                    },
                },
                {
                    data: lastMonth,
                    type: 'line',
                },
                {
                    data: month,
                    type: 'line',
                },
            ]
            
            };
                option && myChart.setOption(option);
        })

        
        }
        
        render() {
            return(
                <div id="main" style={
                   {width:1032,height:400}
                }></div>
            )
        }
    }


export default Sy_11_tong