import { Table } from 'antd';
import axios from 'axios'
import React, { useState, useEffect } from 'react';

const columns = [
    {
      title: '跟进方式',
      dataIndex: 'name',
    },
    {
      title: '个数',
      dataIndex: 'aga',
    },
    {
      title: '占比(%)',
      dataIndex: 'address',
    },
  ];
  function Sy_3_biao1() {
    const [arr,setarr] = useState([])
    useEffect(() => {
      axios.post('http://crm.cimns.com/index.php/bi/customer/recordMode',{
        structure_id: 1,
        user_id: '',
        type: 'year'
      }).then((data)=> {
        setarr(data.data.data)
        console.log(data.data.data);
      })
    },[])

    const data = [];
    for (let i = 0; i < arr.length; i++) {
      data.push({
        key: i,
        name: arr[i].category,
        aga:arr[i].recordNum,
        address:arr[i].proportion
      });
    }
    return(
      <div>
          <Table columns={columns} dataSource={data} pagination={false} bordered='true' />
      </div>
    )
}

export default Sy_3_biao1