//样式
import '../css/sy.css'
import { Input, Select,Button } from 'antd';
import Sy_4_1 from './sy_4_1'
import Sy_4_2 from './sy_4_2'
import Sy_4_3 from './sy_4_3'
import Sy_4_biao from '../../components/sy_4_biao'
import { Route, Switch } from 'react-router';
import { Link } from 'react-router-dom';

const { Option } = Select;

function Sy_4() {
   
    return(
        <div>
            <div className="sy-1-input">
                <Input.Group compact>
                    <Select defaultValue="Option9" style={{width:200,height:34}}>
                        <Option value="Option1">今天</Option>
                        <Option value="Option2">昨天</Option>
                        <Option value="Option3">本周</Option>
                        <Option value="Option4">上周</Option>
                        <Option value="Option5">本月</Option>
                        <Option value="Option6">上月</Option>
                        <Option value="Option7">本季度</Option>
                        <Option value="Option8">上季度</Option>
                        <Option value="Option9">今年</Option>
                        <Option value="Option10">去年</Option>
                        <Option value="Option11">自定义</Option>
                    </Select>
                    <Select defaultValue="Option1" style={{width:120,height:34,marginLeft:20}}>
                        <Option value="Option1">办公室</Option>
                        <Option value="Option2">打杂部</Option>
                    </Select>
                    <Select defaultValue="公司员工" style={{width:120,height:34,marginLeft:20}}  >
                        <Option value="Option1">管理员</Option>
                        <Option value="Option2">管理员</Option>
                    </Select>
                    <Select defaultValue="折线图" style={{width:120,height:34,marginLeft:20}}>
                        <Option value="Option1" ><Link to="/App/box/sy/sy_4/" style={{color:'black'}}> 折线图</Link></Option>
                        <Option value="Option2"><Link to="/App/box/sy/sy_4/sy_4_2" style={{color:'black'}}> 饼状图</Link></Option>
                        <Option value="Option3"><Link to="/App/box/sy/sy_4/sy_4_3" style={{color:'black'}}> 柱形图</Link></Option>
                    </Select>
                    <Button type="primary" style={{marginLeft:20,marginTop:'auto'}}>搜索</Button>
                </Input.Group>
            </div>
            <div className="sy-1-tong" style={{marginBottom:30}}>
                <Switch>
                    <Route path="/App/box/sy/sy_4/" component={Sy_4_1} exact />
                    <Route path="/App/box/sy/sy_4/sy_4_2" component={Sy_4_2}  />
                    <Route path="/App/box/sy/sy_4/sy_4_3" component={Sy_4_3}  />
                </Switch>
            </div>
            <div className="sy-1-biao">
                    <Sy_4_biao></Sy_4_biao>
            </div>
            <div className="sy_4_ti">
                <p>以回款金额排降序，最多展示50条客户数据</p>
            </div>
        </div>
    )
}

export default Sy_4