import React, { } from 'react';
import axios from 'axios';
import * as echarts from 'echarts';


class Sy_16_bing extends React.Component{

    componentDidMount() {
        var value=[]
        var value2=[]
        axios.post('http://crm.cimns.com/index.php/bi/customer/portrait',{
            structure_id: 1,
            user_id: '',
            type: 'year',
            type_analyse: 'level'
        }).then((data)=>{
            console.log(data.data.data);
            for (let i = 0; i < data.data.data.length; i++) {
                value.push(data.data.data[i].allCustomer)
            }
            for (let i = 0; i < data.data.data.length; i++) {
                value2.push(data.data.data[i].dealCustomer)
            }
            var chartDom = document.getElementById('main');
            var myChart = echarts.init(chartDom);
            var option;
            
            var option = {
                title:[
                    {
                        text: '全部客户',
                        left: '220',
                        top:'315'
                    },{
                        text: '成交客户',
                        left: '700',
                        top:'315'
                    },    
                ],
                tooltip: {
                    trigger: 'item',
                },
                
                legend: {
                    data: ['A(重点客户)', 'B(普通客户)', 'C(非优先客户)',],
                    bottom:'bottom'
                },
                series: [
                    {
                        name: '',
                        type: 'pie',
                        radius: ['30%', '50%'],
                        center: ['25%', '43%'],
                        labelLine: {
                            length: 30,
                        },
                        label: {
                            borderWidth: 1,
                            borderRadius: 4,
                        },
                        data: [
                            {value: value[0], name: 'A(重点客户)'},
                            {value: value[1], name: 'B(普通客户)'},
                            {value: value[2], name: 'C(非优先客户)'},
                        ]
                    },{
                        name: '',
                        type: 'pie',
                        radius: ['30%', '50%'],
                        center: ['70%', '43%'],
                        labelLine: {
                            length: 30,
                        },
                        label: {
                            borderWidth: 1,
                            borderRadius: 4,
                        },
                        data: [
                            {value: value2[0], name: 'A(重点客户)'},
                            {value: value2[1], name: 'B(普通客户)'},
                            {value: value2[2], name: 'C(非优先客户)'},
                        ]
                    },
                ]
    
            };
            
                
            option && myChart.setOption(option);
        })
        
    }
        
        render() {
            return(
                <div id="main" style={{width:1032,height:380,}}></div>
            )
        }
    }
export default Sy_16_bing