import { Table } from 'antd';
import '../view/css/biao.css'
import React, { useState, useEffect } from 'react';
import axios from 'axios'
const columns = [
    {
      title: '员工',
      dataIndex: 'name',
      align:'center'
    },
    {
      title: '普通次数',
      dataIndex: 'count_1',
      align:'center'
    },
    {
        title: '请假次数',
        dataIndex: 'count_2',
        align:'center'
      },
      {
        title: '出差次数',
        dataIndex: 'count_3',
        align:'center'
      },
      {
        title: '加班次数',
        dataIndex: 'count_4',
        align:'center'
      },
      {
        title: '差旅报销次数',
        dataIndex: 'count_5',
        align:'center'
      },
      {
        title: '借款申请次数',
        dataIndex: 'count_6',
        align:'center'
      },
  ];
  

  function Sy_30_biao() {
    const [arr,setarr] = useState([])
    useEffect(() => {
      axios.post('http://crm.cimns.com/index.php/bi/examine/statistics',{
        structure_id: 1,
        user_id: "",
        type: "year",
      }).then((data)=> {
        setarr(data.data.data.userList)
        console.log(data.data.data.userList);
      })
    },[])
    const data = [];
    for(let i = 0; i < arr.length;i++) {
      data.push({
        key : i,
        name:arr[i].realname,
        count_1:arr[i].count_1,
        count_2:arr[i].count_2,
        count_3:arr[i].count_3,
        count_4:arr[i].count_4,
        count_5:arr[i].count_5,
        count_6:arr[i].count_6,
       });
  }
      return(
        <div style={{border:'1px solid #ccc'}}>
            <Table columns={columns} dataSource={data}  scroll={{ y: 445}} pagination={false} bordered='true'/>
        </div>
      )
  }

export default Sy_30_biao