import { Table } from 'antd';
import axios from 'axios'
import React, { useState, useEffect } from 'react';

import '../view/css/biao.css'

const columns = [
    {
      title: '员工',
      dataIndex: 'name',
      align:'center'
    },
    {
      title: '填写数',
      dataIndex: 'age',
      align:'center'
    },
    {
        title: '接收人未读数',
        dataIndex: 'agef',
        align:'center'
      },
      {
        title: '未评论数',
        dataIndex: 'ageg',
        align:'center'
      },
      {
        title: '已评论数',
        dataIndex: 'ageh',
        align:'center'
      },
  ];
  

  function Sy_29_biao() {
    const [arr,setarr] = useState([])
    useEffect(() => {
      axios.post('http://crm.cimns.com/index.php/bi/log/statistics',{
        structure_id: 1,
        user_id: '',
        type: "year"
      }).then((data)=> {
        setarr(data.data.data)
        console.log(data.data.data);
      })
    },[])
    const data = [];
    for(let i = 0; i < arr.length;i++) {
      data.push({
        key : i,
        name:arr[i].realname,
        age:arr[i].count,
        agef:arr[i].unReadCont,
        ageg:arr[i].unCommentCount,
        ageh:arr[i].commentCount
      });
    }
      return(
        <div>
            <Table columns={columns} dataSource={data}  scroll={{ y: 445}} pagination={false} bordered='true'/>
        </div>
      )
  }

export default Sy_29_biao