import { Input, Select,Button } from 'antd';
import '../css/sy.css'
import Sy_20_tong from '../../components/sy_20_tong'
import Sy_20_biao from '../../components/sy_20_biao'
import { DatePicker, Space } from 'antd';
const { RangePicker } = DatePicker;
const { Option } = Select;
function Sy_20() {
    return(
        <div>
            <div className="sy-1-input" style={{position:'relative',top:0}}>
                <Input.Group compact>
                <Select defaultValue="Option9" style={{width:200,height:34}}>
                        <Option value="Option1">今天</Option>
                        <Option value="Option2">昨天</Option>
                        <Option value="Option3">本周</Option>
                        <Option value="Option4">上周</Option>
                        <Option value="Option5">本月</Option>
                        <Option value="Option6">上月</Option>
                        <Option value="Option7">本季度</Option>
                        <Option value="Option8">上季度</Option>
                        <Option value="Option9">今年</Option>
                        <Option value="Option10">去年</Option>
                        <Option value="Option11">自定义</Option>
                    </Select>
                    <Select defaultValue="Option1" style={{width:120,height:34,marginLeft:20}}>
                        <Option value="Option1">办公室</Option>
                        <Option value="Option2">打杂部</Option>
                    </Select>
                    <Button type="primary" style={{marginLeft:20,marginTop:'auto'}}>搜索</Button>
                </Input.Group>
            </div>
            <div className="sy_20_title">
                <h3>合同金额排行（按实际下单时间）</h3>
            </div>
            <div className="sy_20_tong">
                <Sy_20_tong></Sy_20_tong>
            </div>
            <div className="sy_20_biao">
                <Sy_20_biao></Sy_20_biao>
            </div>
        </div>
    )
}

export default Sy_20