import React, {} from 'react';
import axios from 'axios';
import * as echarts from 'echarts';

class Sy_4_tong extends React.Component{

    componentDidMount() {
        var sj=[]
        var bl=[]
        axios.post('http://crm.cimns.com/index.php/bi/customer/conversion',{
            structure_id: 1,
            user_id: '',
            type:' year',
        }).then((data)=>{
            console.log(data);
            var chartDom = document.getElementById('main');
            var myChart = echarts.init(chartDom);
            var option;
            for (let t = 0; t < data.data.data.length; t++) {
                sj.push(data.data.data[t].type)
            }
            for (let i = 0; i < data.data.data.length; i++) {
                bl.push(data.data.data[i].proportion)
                
            }
            option = {
                xAxis: {
                    type: 'category',
                    data: sj
                },
                yAxis: [
                    {
                        type: 'value',
                        scale: true,
                        max: 1,
                        min: 0,
                        boundaryGap: [0.2, 0.2],
                        axisLabel: {
                            formatter: '{value} %'
                        }
                    }
                ],
                series: [{
                    data: bl,
                    type: 'bar'
                }]
            };
            option && myChart.setOption(option);
        })
            
        }
        
        render() {
            return(
                <div id="main" style={
                   {width:1032,height:420}
                }></div>
            )
        }
    }

export default Sy_4_tong