import React, { useState, useEffect} from 'react';
import axios from 'axios'
import './css/hsz.css'
function Gl_hsz(){
    const [data, setData] = useState([]);
    useEffect(() => {
      const FetchData = async () => {
        const result = await axios.post(
            'http://crm.cimns.com/index.php/work/trash/index',{
              page: 1,
              limit: 15,
              types: "list",
              isSub: 0,
              type: 1
            }
          );
        console.log(result.data.data.list);
        setData(result.data.data.list);
      };
      FetchData(); 
    }, []);
    return(
        <div className='ly'>
            <div className="hsz-header">
                <h2>回收站</h2>
            </div>
            <div className="hsz-con">
                {data.map((item,i)=>(
                     <ul key={item.task_id}>
                        <li>{item.name}</li>    
                        <span>{item.stop_time}</span>   
                 </ul>
                ))}
               
            </div>
        </div>
    )
}
export default Gl_hsz;