import React from 'react'
import { Menu, Button } from 'antd';
import {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
    AuditOutlined,
    DesktopOutlined,
  } from '@ant-design/icons';
  import './css/gl.css'
  import {Switch,Route,Link} from 'react-router-dom'
  import Gl_wdrw from './gl_wdrw'
  import Gl_rwrl from './gl_rwrl'    
  import Gl_xm from './gl_xm'
  import Gl_tjfx from './gl_tjfx'
  import Gl_gd from './gl_gd'
  import Gl_bq from './gl_bq'
  import Gl_hsz from './gl_hsz'
  const { SubMenu } = Menu;
class Gl extends React.Component {
    state = {
        collapsed: false,
      };
    
      toggleCollapsed = () => {
        this.setState({
          collapsed: !this.state.collapsed,
        });
      };
      render() {
        return (
        <div className="l-khbox clearfix" style={{background:'#f5f6f9'}}>
            
            <div className = 'cd clearfix' id='submit'>
            
            <Menu
              defaultSelectedKeys={['1']}
              defaultOpenKeys={['sub1']}
              mode="inline"
              theme="dark"
              inlineCollapsed={this.state.collapsed}
            >
              <div className="dj"><p>快速创建</p></div>
                  <SubMenu key="sub1" icon={<DesktopOutlined />} title="工作台">
                   
                      <Menu.Item key="1"> 
                      <Link to="/App/box/gl">
                        我的任务
                        </Link>
                        </Menu.Item>
                   
                   
                      <Menu.Item key="2">
                         <Link to="/App/box/gl/gl_rwrl">
                        任务日历 
                        </Link>
                        </Menu.Item>
                   
                  </SubMenu>
                  <SubMenu key="sub2" icon={<AuditOutlined />} title="项目">
                  
                     <Menu.Item key="3">
                       <Link to="/App/box/gl/gl_xm">
                       ABCD
                        </Link>
                       </Menu.Item>
                 
                  </SubMenu>
                  <Menu.Item key="4" icon={<AuditOutlined />}>
                  <Link to="/App/box/gl/gl_tjfx">
                      统计分析
                  </Link>
                  </Menu.Item>
                  <Menu.Item key="5" icon={<AuditOutlined />}>
                  <Link to="/App/box/gl/gl_gd">
                     归档项目
                  </Link>
                  </Menu.Item>
                  <SubMenu key="sub3" icon={<AuditOutlined />} title="标签">
                  
                     <Menu.Item key="6">
                       <Link to="/App/box/gl/gl_bq">
                       1111 
                       </Link>
                       </Menu.Item>
                 
                  </SubMenu>
                  <SubMenu key="sub4" icon={<AuditOutlined />} title="回收站">
                     <Menu.Item key="7">
                       <Link to="/App/box/gl/gl_hsz">
                       回收站 
                       </Link>
                      </Menu.Item>
                 
                  </SubMenu>
                <div>
                <Button type="primary" onClick={this.toggleCollapsed} style={{ marginBottom: 16 }} ref={el => (this.button = el)} >
                      {React.createElement(this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined)}
                </Button>
              </div>
              
            </Menu>            
          </div>
            
          {/* <div className='znr'> */}
              <Switch>
                  <Route path="/App/box/gl" component={Gl_wdrw}exact/>
                  <Route path="/App/box/gl/gl_rwrl" component={Gl_rwrl}/>
                  <Route path="/App/box/gl/gl_xm" component={Gl_xm}/>
                  <Route path="/App/box/gl/gl_tjfx" component={Gl_tjfx}/>
                  <Route path="/App/box/gl/gl_gd" component={Gl_gd}/>
                  <Route path="/App/box/gl/gl_bq" component={Gl_bq}/>
                  <Route path="/App/box/gl/gl_hsz" component={Gl_hsz}/>
              </Switch>
          </div>
        // </div>
          
          
        );
      }
}
export default Gl