import React from 'react'
import * as echarts from 'echarts';
import axios from 'axios'


class Sy_3_bing extends React.Component{

    componentDidMount() {
        axios.post('http://crm.cimns.com/index.php/bi/customer/recordMode',{
            structure_id: 1,
            user_id: '',
            type:' year',
        }).then((data)=>{
            console.log(data);
            var chartDom = document.getElementById('main');
            var myChart = echarts.init(chartDom);
            var option;
            option = {
                tooltip: {
                    trigger: 'item'
                },
                legend: {
                    orient: 'vertical',
                    bottom:'bottom'
                },
                series: [
                    {
                        name: '访问来源',
                        type: 'pie',
                        radius: '50%',
                        data: [
                            {value: data.data.data[0].recordNum, name: data.data.data[0].category},
                            {value: data.data.data[1].recordNum, name: data.data.data[1].category},
                            {value: data.data.data[2].recordNum, name: data.data.data[2].category},
                            {value: data.data.data[3].recordNum, name: data.data.data[3].category},
                            {value: data.data.data[4].recordNum, name: data.data.data[4].category}
                        ],
                        emphasis: {
                            itemStyle: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }
                ]
            };option && myChart.setOption(option);
        })
        
            
        
    }
        
        render() {
            return(
                <div id="main" style={{width:1032,height:380,}}></div>
            )
        }
    }
export default Sy_3_bing