import { Table } from 'antd';
import '../view/css/biao.css'
import axios from 'axios'
import React, { useState, useEffect } from 'react';



  function Sy_12_biao() {
    const [arr,setarr] = useState([])
    useEffect(() => {
      axios.post('http://crm.cimns.com/index.php/bi/contract/analysis',{
        structure_id: 1,
        user_id: '',
        year: 2021,
        type: 'back'
      }).then((data)=> {
        console.log(data.data.data);
        setarr(data.data.data)
        
      })
    },[])
    const columns = [
        {
          title: '日期',
          width: 185,
          dataIndex: 'name',
          key: 'name',
          fixed: 'left',
          fontSize:'14px',
          align:'center'
        },
        { title: '1月', dataIndex: 'address1', key: '1',width:100,align:'center' },
        { title: '2月', dataIndex: 'address2', key: '2',width:100,align:'center' },
        { title: '3月', dataIndex: 'address3', key: '3',width:100,align:'center' },
        { title: '4月', dataIndex: 'address4', key: '4',width:100,align:'center' },
        { title: '5月', dataIndex: 'address5', key: '5',width:100,align:'center' },
        { title: '6月', dataIndex: 'address6', key: '6',width:100,align:'center' },
        { title: '7月', dataIndex: 'address7', key: '7',width:100,align:'center' },
        { title: '8月', dataIndex: 'address8', key: '8',width:100,align:'center' },
        { title: '9月', dataIndex: 'address9', key: '9',width:100,align:'center' },
        { title: '10月', dataIndex: 'address10', key: '10',width:100,align:'center' },
        { title: '11月', dataIndex: 'address11', key: '11',width:100,align:'center' },
        { title: '12月', dataIndex: 'address12', key: '12',width:100,align:'center' },
      ];
      let data=[]
      if (arr.length === 0) {
        
      }else{
        data = [
          {
            key: '1',
            name: '当月合同数量（个）',
            address1: arr[0].month,
            address2:arr[1].month,
            address3:arr[2].month,
            address4:arr[3].month,
            address5:arr[4].month,
            address6:arr[5].month,
            address7:arr[6].month,
            address8:arr[7].month,
            address9:arr[8].month,
            address10:arr[9].month,
            address11:arr[10].month,
            address12:arr[11].month,
          },
          {
            key: '2',
            name: '上月合同数量（个）',
            address1: arr[0].lastMonth,
            address2:arr[1].lastMonth,
            address3:arr[2].lastMonth,
            address4:arr[3].lastMonth,
            address5:arr[4].lastMonth,
            address6:arr[5].lastMonth,
            address7:arr[6].lastMonth,
            address8:arr[7].lastMonth,
            address9:arr[8].lastMonth,
            address10:arr[9].lastMonth,
            address11:arr[10].lastMonth,
            address12:arr[11].lastMonth,
          },
          {
              key: '3',
              name: '去年当月合同数量（个）',
            address1: arr[0].year_on_year,
            address2:arr[1].year_on_year,
            address3:arr[2].year_on_year,
            address4:arr[3].year_on_year,
            address5:arr[4].year_on_year,
            address6:arr[5].year_on_year,
            address7:arr[6].year_on_year,
            address8:arr[7].year_on_year,
            address9:arr[8].year_on_year,
            address10:arr[9].year_on_year,
            address11:arr[10].year_on_year,
            address12:arr[11].year_on_year,
            },
            {
              key: '4',
              name: '环比增长（%）',
              address1: arr[0].chain_ratio,
              address2:arr[1].chain_ratio,
              address3:arr[2].chain_ratio,
              address4:arr[3].chain_ratio,
              address5:arr[4].chain_ratio,
              address6:arr[5].chain_ratio,
              address7:arr[6].chain_ratio,
              address8:arr[7].chain_ratio,
              address9:arr[8].chain_ratio,
              address10:arr[9].chain_ratio,
              address11:arr[10].chain_ratio,
              address12:arr[11].chain_ratio,
            },
            {
              key: '5',
              name: '同比增长（%）',
              address1: arr[0].lastYeatMonth,
              address2:arr[1].lastYeatMonth,
              address3:arr[2].lastYeatMonth,
              address4:arr[3].lastYeatMonth,
              address5:arr[4].lastYeatMonth,
              address6:arr[5].lastYeatMonth,
              address7:arr[6].lastYeatMonth,
              address8:arr[7].lastYeatMonth,
              address9:arr[8].lastYeatMonth,
              address10:arr[9].lastYeatMonth,
              address11:arr[10].lastYeatMonth,
              address12:arr[11].lastYeatMonth,
            },
        ];
      }
      
      return(
        <div>
            <Table columns={columns} dataSource={data} scroll={{ x: 1400 }} pagination={false} bordered='true' />
        </div>
      )
  }

export default Sy_12_biao