import React from 'react'
import * as echarts from 'echarts';


class Sy_7_tong extends React.Component{

    componentDidMount() {
        var chartDom = document.getElementById('main');
        var myChart = echarts.init(chartDom);
        var option;

        option = {
            tooltip: {
                trigger: 'item',
                formatter: '{a} <br/>{b} : {c}%'
            },
            
            legend: {
                orient: 'vertical',
                left: 'left',
                data: [ '访问', ]
            },
        
            series: [
                {
                    name: '漏斗图',
                    type: 'funnel',
                    width: '50%',
                    height: '15%',
                    left: '25%',
                    top: '30%',
                    data: [
                        {value: 60, name: '需求分析(0)',},
                    ]
                },
                {
                    name: '金字塔',
                    type: 'funnel',
                    width: '50%',
                    height: '15%',
                    left: '25%',
                    top: '15%',
                    sort: 'ascending',
                    data: [
                        {value: 60, name: '验证客户(0)'},
                    ]
                },
                
            ]
        };
            option && myChart.setOption(option);
        }
        
        render() {
            return(
                <div id="main" style={
                   {width:1032,height:420}
                } ></div>
            )
        }
    }

export default Sy_7_tong