import React,{Component} from 'react'
import * as echarts from 'echarts';
import axios from 'axios';


class Sy_3_tong extends React.Component{

    componentDidMount() {
        var rq=[]
        var jy=[]
        axios.post('http://crm.cimns.com/index.php/bi/customer/recordTimes',{
            structure_id: 1,
            user_id: '',
            type:' year',
        }).then((data)=>{
            for (let t = 0; t < data.data.data.length; t++) {
                rq.push(data.data.data[t].type)
            }
            for (let i = 0; i < data.data.data.length; i++) {
                jy.push(data.data.data[i].customerCount)
            }

        var chartDom = document.getElementById('main');
        var myChart = echarts.init(chartDom);
        var option;

            option = {
                xAxis: {
                    type: 'category',
                    data: rq
                },
                yAxis: [
                    {
                        type: 'value',
                        scale: true,
                        name: '新增客户数',
                        max: 1,
                        min: 0,
                        boundaryGap: [0.2, 0.2],
                        axisLabel: {
                            formatter: '{value} 个'
                        }
                    }
                ],
                series: [{
                    data: jy,
                    type: 'bar'
                }]
            };
            option && myChart.setOption(option);
        },(err)=>{
            console.log(err)
        })
    }
        render() {
            return(
                <div id="main" style={
                   {width:1032,height:420}
                }></div>
            )
        }
    }

export default Sy_3_tong