import React, { useState, useEffect} from 'react';
import axios from 'axios'
import {Empty,Input, Space,Select, Modal, Button,Pagination} from 'antd';
import './css/kh_3.css'


const { Search } = Input;

  const onSearch = value => console.log(value);
  const { Option } = Select;

  function handleChange(value) {
    console.log(`selected ${value}`);
  }
function Kh_10(){
    const [visible, setVisible] = React.useState(false);
    const [confirmLoading, setConfirmLoading] = React.useState(false);
    const [modalText, setModalText] = React.useState('基本信息');
  
    const showModal = () => {
      setVisible(true);
    };
    const showModal1 = () => {
        setVisible(true);
      };
  
    const handleOk = () => {
      setModalText('lie modal will be closed after two seconds');
      setConfirmLoading(true);
      setTimeout(() => {
        setVisible(false);
        setConfirmLoading(false);
      }, 2000);
    };
  
    const handleCancel = () => {
      console.log('Clicked cancel button');
      setVisible(false);
    };
  
    const [data, setData] = useState([]);
    useEffect(() => {
      const fetchData = async () => {
        const result = await axios.post(
            'http://crm.cimns.com/index.php/crm/product/index',{
              page: 1,
              limit: 15,
              search: '',
            }
          )
        console.log(result.data);
        setData(result.data.data.list);
      };
      fetchData(); 
    }, []);
    console.log(data)
   
    return(
        <div className="kh_3">
            <div className="kh_3-header clearfix">
                <div className="kh_3-header-title">
                    <h2>产品管理</h2>
                </div>
                <div className="kh_3-header-ss">
                    <Space direction="vertical">
                        <Search placeholder="input search text" onSearch={onSearch} style={{ width: 200 }} />
                    </Space>
                </div>
                <div className="kh_3-header-cj clearfix">
                    <div className="kh_3-header-cj-1">
                    <>
                            <Button type="primary" onClick={showModal}>
                                新建产品
                            </Button>
                            <Modal
                                title="新建产品"
                                visible={visible}
                                onOk={handleOk}
                                confirmLoading={confirmLoading}
                                onCancel={handleCancel}
                            >
                                <div style={{height:'600px'}}>
                                    <h4>基本信息</h4>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>产品名称</h6>
                                        <Input style={{width:'284px',margin:'0 10px'}}/>  
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>产品类型</h6>
                                        <>
                                            <Select defaultValue="请选择" style={{ width: 284 }} onChange={handleChange}>
                                                <Option value="1">默认</Option>
                                            </Select>
                                        </>
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>产品编号</h6>
                                        <Input style={{width:'284px',margin:'0 10px'}}/>  
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>是否上架</h6>
                                        <>
                                            <Select defaultValue="请选择" style={{ width: 284 }} onChange={handleChange}>
                                                <Option value="1">上架</Option>
                                                <Option value="2">下架</Option>
                                                
                                            </Select>
                                        </>
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>单位</h6>
                                        <>
                                            <Select defaultValue="请选择" style={{ width: 284 }} onChange={handleChange}>
                                                <Option value="1">个</Option>
                                                <Option value="2">块</Option>
                                                <Option value="3">只</Option>
                                                <Option value="4">把</Option>
                                                <Option value="5">枚</Option>
                                                <Option value="6">瓶</Option>
                                                <Option value="7">盒</Option>
                                                <Option value="8">台</Option>
                                                <Option value="9">吨</Option>
                                                <Option value="10">千克</Option>
                                                <Option value="11">米</Option>
                                                <Option value="12">箱</Option>

                                            </Select>
                                        </>
                                    </div>
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>标准价格(元)</h6>
                                        <Input style={{width:'284px',margin:'0 10px'}}/>  
                                    </div>
                                   
                                    <div style={{float:'left',width:'309px',height:'77px'}}>
                                        <h6 style={{margin:'5px 10px'}}>产品描述</h6>
                                        <Input style={{width:'284px',margin:'0 10px'}}/>  
                                    </div>
                                </div>
                            </Modal>
                        </>
                    </div>
                    <div className="kh_3-header-cj-2">
                        <>
                            <Select defaultValue="jack" style={{ width: 70 }} onChange={handleChange}>
                                <Option value="jack">导入</Option>
                                <Option value="lucy">导出</Option>
                            </Select>
                        </>
                    </div>
                </div>
            </div>
            <div className="kh_3-con-top">
                    <div className="kh_3-con-top-1">场景:</div>
                    <div className="kh_3-con-top-2">
                    <>
                        <Select defaultValue="jack" style={{ width: 120 }} bordered={false}>
                            <Option value="jack">全部产品</Option>
                           
                        </Select>
                    </>
                    </div>
                    <div className="kh_3-con-top-3">
                    <>
                        <Button type="primary" onClick={showModal1}>
                            高级筛选
                        </Button>
                        <Modal
                            // title="Title"
                            // visible={visible}
                            // onOk={handleOk}
                            // confirmLoading={confirmLoading}
                            // onCancel={handleCancel}
                        >
                        <p>{modalText}</p>
                        </Modal>
                    </>
                    </div> 
                </div>
            <div className="kh_3-con">
                <div className="kh_3-con-con">
                    <div className="kh_3-con-con-1">
                        <ul>
                                <li>产品名称</li>
                                <li>产品类型</li>
                                <li>产品编码</li>
                                <li>是否上架</li>
                                <li>单位</li>
                                <li>标准价格</li>
                                <li>产品描述</li>
                                <li>创建人</li>
                                <li>更新时间</li>
                                <li>手机</li>
                                <li>创建时间</li>
                                <li>负责人</li>
                               
                               
                            </ul>
                    </div>
                    <div className="kh_3-con-con-2">
                            {data.map((item)=>( 
                                <ul key={item.create_user_id}>
                                        <li> {item.name}</li>  
                                        <li> {item.category_id_info}</li>
                                        <li> {item.num}</li>
                                        <li> {item.status}</li>
                                        <li> {item.unit}</li>
                                        <li> {item.price}</li>
                                        <li> {item.description}</li>
                                        <li> {item.create_user_id_info.realname}</li>
                                        <li> {item.update_time}</li>
                                        <li> {item.create_time}</li>
                                        <li> {item.owner_user_id_info.realname}</li>
                                    
                                    </ul>
                                ))}
                    </div>
                </div>
            </div>
            <div className="kh_3-footer">
                    <>
                        <Pagination size="small" total={50} pagination={false} showSizeChanger showQuickJumper />
                    </>
                </div>
        </div>
    )
}
export default Kh_10