import React, {Component} from "react";
import * as echarts from 'echarts';

class Echarts2 extends Component {
    componentDidMount() {
        var chartDom = document.getElementById('main2');
        var myChart = echarts.init(chartDom);
        var option;
        option = {
            tooltip: {
                trigger: 'axis',
                axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                    type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis: [
                {
                    type: 'category',
                    data: ['2021-01', '2021-03', '2021-05', '2021-07', '2021-09', '2021-11',],
                    axisTick: {
                        alignWithLabel: true
                    }
                }
            ],
            yAxis: [
                {
                    type: 'value'
                }
            ],
            series: [
                {
                    name: '直接访问',
                    type: 'bar',
                    barWidth: '60%',
                    data: [0, 0, 0, 0, 0, 0, 0]
                }
            ]
        };
        option && myChart.setOption(option);
    }
    render(){
        return(
            <div>
                <div id = "main2"style = {{width: 560,height: 220,}} ></div>
            </div>
        )
    }
}

export default Echarts2