import React, {useState,useEffect } from 'react';
import axios from 'axios';
import * as echarts from 'echarts';
import { Table } from 'antd';

const columns = [
    {
      title: '姓名',
      dataIndex: 'name',
      width: 150,
    },
    {
      title: '部门',
      dataIndex: 'aga',
      width: 150,
    },
    {
        title: '公海池领取客户数',
        dataIndex: 'agb',
        width: 150,
      },
      {
        title: '进入公海客户数',
        dataIndex: 'agc',
        width: 150,
      },
      {
        title: '客户总数',
        dataIndex: 'agd',
        width: 150,
      },
  ];
  
  

  function Biao5() {
    const [arr,setarr] = useState([])
    useEffect(() => {
      axios.post('http://crm.cimns.com/index.php/bi/customer/poolList',{
        structure_id: 1,
        user_id: '',
        type: 'year'
      }).then((data)=> {
        console.log(data.data.data);
        setarr(data.data.data)
      })
    },[])
    const data = [];
    for (let i = 0; i < arr.length; i++) {
      data.push({
        key: i,
        name: arr[i].realname,
        aga: arr[i].username,
        agb:arr[i].receive,
        agc:arr[i].put_in,
        agd: arr[i].customer_num,
      });
    }

      return(
        <div>
            <Table columns={columns} dataSource={data}  scroll={{ y: 370 }} pagination={false} bordered='true'/>
        </div>
      )
  }

export default Biao5