import { Table } from 'antd';
import '../view/css/biao.css'
import axios from 'axios'
import React, { useState, useEffect } from 'react';
const columns = [
    {
      title: '公司总排名',
      dataIndex: 'name',
      align:'center'
    },
    {
      title: '签订人',
      dataIndex: 'user_name',
      align:'center'
    },
    {
        title: '部门',
        dataIndex: 'age',
        align:'center'
      },
      {
        title: '合同金额(元)',
        dataIndex: 'agef',
        align:'center'
      },
  ];
  


  function Sy_20_biao() {
    const [arr,setarr] = useState([])
    useEffect(()=>{
      axios.post('http://crm.cimns.com/index.php/bi/ranking/contract',{
        structure_id: 1,
          type: "year",
      })
      .then((data) => {
        setarr(data.data.data)
        console.log(data.data.data)
      })
    },[])
    const data = [];
    for(let i = 0; i < arr.length;i++) {
      data.push({
        key : i,
        name:i+1,
        user_name:arr[i].user_name,
        age:arr[i].structure_name,
        agef:arr[i].money,
      });
    }
      return(
        <div>
            <Table columns={columns} dataSource={data}  scroll={{ y: 370 }} pagination={false} bordered='true'/>
        </div>
      )
  }

export default Sy_20_biao