import { Table } from 'antd';
import '../view/css/biao.css'
import axios from 'axios'
import React, { useState, useEffect } from 'react';

  function Sy_16_biao() {
    const [arr,setarr] = useState([])
    useEffect(() => {
      axios.post('http://crm.cimns.com/index.php/bi/customer/portrait',{
        structure_id: 1,
        user_id: '',
        type: 'year',
        type_analyse: 'level'
      }).then((data)=> {
        setarr(data.data.data)
      })
    },[])
    const columns = [
        {title: '客户行业', dataIndex: 'name', key: 'name', align:'center'},
        { title: 'A  (重要客户)', dataIndex: 'address1', key: '1',align:'center' },
        { title: 'B  (普通客户)', dataIndex: 'address2', key: '2',align:'center' },
        { title: 'C  (非优先客户)', dataIndex: 'address3', key: '3',align:'center' },
      ];
      
      let data=[]
      if (arr.length === 0) {
        
      }else{
        data = [
          {
            key: '1',
            name: '所有客户(个)',
            address1: arr[0].allCustomer,
            address2:arr[1].allCustomer,
            address3:arr[2].allCustomer,
            
          },
          {
            key: '2',
            name: '成交客户(个)',
            address1: arr[0].dealCustomer,
            address2:arr[1].dealCustomer,
            address3:arr[2].dealCustomer,
           
          },
        ];
      }
      return(
        <div>
            <Table columns={columns} dataSource={data}  pagination={false} bordered='true' />
        </div>
      )
  }

export default Sy_16_biao