import { Table, Tag, Space } from 'antd';
import '../view/css/biao.css'
import axios from 'axios'
import React, { useState, useEffect } from 'react';
const columns = [
    {
      title: '阶段',
      dataIndex: 'name',
      key: 'name',
      align:'center',
    },
    {
      title: '金额',
      dataIndex: 'aga',
      key: 'age',
      align:'center',
    },
    {
      title: '商机数',
      dataIndex: 'agb',
      key: 'address',
      align:'center',
    },
  ];
  
  function Sy_7_biao() {
    const [arr,setarr] = useState([])
    useEffect(() => {
      axios.post('http://crm.cimns.com/index.php/bi/business/funnel',{
        structure_id: 1,
        user_id: '',
        type: 'year'
      }).then((data)=> {
        setarr(data.data.data.list)
        console.log(data.data.data);
      })
    },[])

    const data = [];
    for (let i = 0; i < arr.length; i++) {
      data.push({
        key: i,
        name:arr[i].name,
        aga:arr[i].money,
        agb:arr[i].count

      });
    }
      return(
        <div >
            <Table columns={columns} dataSource={data} pagination={false} bordered='true' style={{height:400}}/>
        </div>
      )
  }

export default Sy_7_biao