import React, { } from 'react';
import axios from 'axios';
import * as echarts from 'echarts';


class Tong5 extends React.Component{

    componentDidMount() {
        var rq=[]
        var fr=[]
        var sd=[]
        axios.post('http://crm.cimns.com/index.php/bi/customer/pool',{
            structure_id: 1,
            user_id: '',
            type:' year',
        }).then((data)=>{
            console.log(data);
            for (let t = 0; t < data.data.data.length; t++) {
                rq.push(data.data.data[t].type)
            }
            for (let i = 0; i < data.data.data.length; i++) {
                fr.push(data.data.data[i].put_in)
            }
            for (let j = 0; j < data.data.data.length; j++) {
                sd.push(data.data.data[j].receive)
            }
            var chartDom = document.getElementById('main');
            var myChart = echarts.init(chartDom);
            var option;

            option = {
                xAxis: {
                    type: 'category',
                    data: rq
                },
                yAxis: [
                    {
                        type: 'value',
                        scale: true,
                        name: '进入公海客户数',
                        max:5,
                        min: 0,
                        boundaryGap: [0.2, 0.2],
                        axisLabel: {
                            formatter: '{value} 个'
                        }
                    },{
                        type: 'value',
                        scale: true,
                        name: '公海池领取客户数',
                        max: 5,
                        min: 0,
                        boundaryGap: [0.2, 0.2],
                        axisLabel: {
                            formatter: '{value} 次'
                        }
                    }
                ],
                
                series: [{
                    data: fr,
                    type: 'bar'
                },
                {
                    data: sd,
                    type: 'bar'
                }
            ],
                
            };
            option && myChart.setOption(option);
        })

            
        }
        
        render() {
            return(
                <div id="main" style={
                   {width:1032,height:420}
                }></div>
            )
        }
    }

export default Tong5