import React, { } from 'react';
import axios from 'axios';
import * as echarts from 'echarts';


class Sy_17_bing extends React.Component{

    componentDidMount() {
        var source=[]
        var arr=[]
        axios.post('http://crm.cimns.com/index.php/bi/customer/portrait',{
            structure_id: 1,
            user_id: '',
            type: 'year',
            type_analyse: 'source'
        }).then((data)=>{
            console.log(data.data.data);
            for (let i = 0; i < data.data.data.length; i++) {
                source.push(data.data.data[i].source)
                
            }
            arr=data.data.data
            var chartDom = document.getElementById('main');
            var myChart = echarts.init(chartDom);
            var option;
            
            var option = {
                title:[
                    {
                        text: '全部客户',
                        left: '220',
                        top:'315'
                    },{
                        text: '成交客户',
                        left: '700',
                        top:'315'
                    },    
                ],
                tooltip: {
                    trigger: 'item',
                },
                
                legend: {
                    type: 'scroll',
                    data: source,
                    bottom:'bottom'
                },
                series: [
                    {
                        name: '',
                        type: 'pie',
                        radius: ['30%', '50%'],
                        center: ['25%', '43%'],
                        labelLine: {
                            length: 30,
                        },
                        label: {
                            borderWidth: 1,
                            borderRadius: 4,
                        },
                        data: [
                            {value: arr[0].allCustomer, name: '促销活动'},
                            {value: arr[1].allCustomer, name: '搜索引擎'},
                            {value: arr[2].allCustomer, name: '广告'},
                            {value: arr[3].allCustomer, name: '转介绍'},
                            {value: arr[4].allCustomer, name: '线上注册'},
                            {value: arr[5].allCustomer, name: '线上询价'},
                            {value: arr[6].allCustomer, name: '预约上门'},
                            {value: arr[7].allCustomer, name: '陌拜'},
                            {value: arr[8].allCustomer, name: '招商资源'},
                            {value: arr[9].allCustomer, name: '公司资源'},
                            {value: arr[10].allCustomer, name: '展会资源'},
                            {value: arr[11].allCustomer, name: '个人资源'},
                            {value: arr[12].allCustomer, name: '电话咨询'},
                            {value: arr[13].allCustomer, name: '邮件咨询'},
                        ]
                    },{
                        name: '',
                        type: 'pie',
                        radius: ['30%', '50%'],
                        center: ['70%', '43%'],
                        labelLine: {
                            length: 30,
                        },
                        label: {
                            borderWidth: 1,
                            borderRadius: 4,
                        },
                        data: [
                            {value: arr[0].dealCustomer, name: '促销活动'},
                            {value: arr[1].dealCustomer, name: '搜索引擎'},
                            {value: arr[2].dealCustomer, name: '广告'},
                            {value: arr[3].dealCustomer, name: '转介绍'},
                            {value: arr[4].dealCustomer, name: '线上注册'},
                            {value: arr[5].dealCustomer, name: '线上询价'},
                            {value: arr[6].dealCustomer, name: '预约上门'},
                            {value: arr[7].dealCustomer, name: '陌拜'},
                            {value: arr[8].dealCustomer, name: '招商资源'},
                            {value: arr[9].dealCustomer, name: '公司资源'},
                            {value: arr[10].dealCustomer, name: '展会资源'},
                            {value: arr[11].dealCustomer, name: '个人资源'},
                            {value: arr[12].dealCustomer, name: '电话咨询'},
                            {value: arr[13].dealCustomer, name: '邮件咨询'},
                        ]
                    },
                ]
    
            };
            
                
            option && myChart.setOption(option);
        })
        
        }
        
        render() {
            return(
                <div id="main" style={{width:1032,height:380,}}></div>
            )
        }
    }
export default Sy_17_bing