import { Table } from 'antd';
import '../view/css/biao.css'
import React, { useState, useEffect } from 'react';
import axios from 'axios'

  

  function Sy_31_biao() {
    const columns = [
      {
        title: '时间',
        dataIndex: 'count_1',
        align:'center'
      },
      {
        title: '合同金额(元)',
        dataIndex: 'count_2',
        align:'center'
      },
      {
          title: '目标(元)',
          dataIndex: 'count_3',
          align:'center'
        },
        {
          title: '完成率(%)',
          dataIndex: 'count_4',
          align:'center'
        },
    ];
    const [arr,setarr] = useState([])
    useEffect(() => {
      axios.post('http://crm.cimns.com/index.php/bi/achievement/statistics',{
        year: 2021,
        status: 1,
        structure_id: 1,
        user_id: "",
      }).then((data)=> {
        setarr(data.data.data)
        console.log(data.data.data);
      })
    },[])
    const data = [];
    for(let i = 0; i < arr.length;i++) {
      data.push({
        key : i,
        count_1:arr[i].month,
        count_2:arr[i].receivables,
        count_3:arr[i].achiement,
        count_4:arr[i].rate,
       });
  }
      return(
        <div style={{border:'1px solid #ccc'}}>
            <Table columns={columns} dataSource={data}  scroll={{ y: 445}} pagination={false} bordered='true'/>
        </div>
      )
  }

export default Sy_31_biao