import 'antd/dist/antd.css'
import React,{useState} from 'react';
import { Modal, Button ,Row, Col } from 'antd';
import './css/wdrw.css' 
import {
    PlusOutlined
  } from '@ant-design/icons';
  import { Tabs } from 'antd';

const { TabPane } = Tabs;
const style = { height:130, padding: '8px 0',backgroundColor:'#fff',borderRadius:5 };

    

function Gl_xm(){
    const [isModalVisible, setIsModalVisible] = useState(false);
    const showModal = () => {
        setIsModalVisible(true);
      };
    
      const handleOk = () => {
        setIsModalVisible(false);
      };
    
      const handleCancel = () => {
        setIsModalVisible(false);
      };
      function callback(key) {
        console.log(key);
      }
    return(
        <div className="ly">
           <div className="gl_xm-header" style={{height:'74px',backgroundColor:'#fff',paddingLeft:'10px'}}>
                <h5 style={{fontSize: "16px",color:"#333"}}>wsertyuio</h5>
                <Tabs defaultActiveKey="1" onChange={callback}>
                    <TabPane tab="任务版" key="1">
                    <Row gutter={16}>
                        <Col className="gutter-row" span={5}>
                            <div style={style}>
                                <div className='wdrw'>
                                    <h6><b>要做</b>  0/0</h6>
                                    <div></div>
                                    <p><PlusOutlined style={{color:'#3e8ee9'}}/> 新建任务</p>
                                </div>
                            </div>
                        </Col>
                        <Col className="gutter-row" span={5}>
                            <div style={style}>
                                <div className='wdrw'>
                                    <h6><b>在做</b>  0/0</h6>
                                    <div></div>
                                    <p><PlusOutlined style={{color:'#3e8ee9'}}/> 新建任务</p>
                                </div>
                            </div>
                        </Col>
                        <Col className="gutter-row" span={5}>
                            <div style={style}>
                                <div className='wdrw'>
                                    <h6><b>待定</b>  0/0</h6>
                                    <div></div>
                                    <p><PlusOutlined style={{color:'#3e8ee9'}}/> 新建任务</p>
                                </div>
                            </div>
                        </Col>
                    </Row>
                    </TabPane>
                    <TabPane tab="附件" key="2">
                        <div style={{height:'600px'}}>
                            <div style={{height:'30px'}}>
                                <ul style={{height:'30px',display:"flex",background:'#F5F5F5',textAlign:'center',border:"1px solid #ccc",lineHeight:"30px"}}>
                                    <li style={{flex:"1"}}>附件名称</li>
                                    <li style={{flex:"1"}}>附件大小</li>
                                    <li style={{flex:"1"}}>上传人</li>
                                    <li style={{flex:"1"}}>上传时间</li>
                                    <li style={{flex:"1"}}>操作</li>
                                </ul>
                            </div>
                            <div style={{height:'550px',backgroundColor:"#fff",position: "relative"}}>
                               <div style={{width:'200px',height:'200px',position:'absolute',left:'50%',top:'50%',marginLeft:"-50px",marginTop:'-50px'}}>暂无数据</div>
                            </div>
                        </div>
                    </TabPane>
                    <TabPane tab="任务统计" key="3">
                        <div style={{height:"600"}}>
                            <h4 style={{fontSize:'13px',color:'#333',marginLeft:'8px'}}>任务总览</h4>
                            <div style={{width:'1288px',height:'204px',backgroundColor:'#fff',display:'flex'}}>
                                <div style={{height:'173px',flex:'1',padding:'10px'}}>
                                    <div style={{width:'120px',height:'120px',float:'left',border:'5px solid #E8F2FA',margin:'20px 110px',borderRadius:'50%'}}>
                                        <span style={{display:'block',textAlign:"center",lineHeight:'70px'}}>完成率</span>
                                        <span style={{display:'block',textAlign:"center"}}>0%</span>
                                    </div>
                                    <div style={{width:'120px',height:'120px',float:'left',border:'5px solid #E8F2FA',margin:'20px',borderRadius:'50%'}}>
                                        <span style={{display:'block',textAlign:"center",lineHeight:'70px'}}>逾期率</span>
                                        <span style={{display:'block',textAlign:"center"}}>0%</span>
                                    </div>
                                </div>
                                <div style={{height:'173px',flex:'1',paddingTop:'80px'}}>
                                    <ul style={{height:'25px',margin:"0px",borderBottom:"1px solid #ccc",width:'340px',fontSize:'8px'}}>
                                        <li style={{float:'left',textAlign:'center',width:'64px'}}>0</li>
                                        <li style={{float:'left',textAlign:'center',width:'64px'}}>0</li>
                                        <li style={{float:'left',textAlign:'center',width:'64px'}}>0</li>
                                        <li style={{float:'left',textAlign:'center',width:'64px'}}>0</li>
                                        <li style={{float:'left',textAlign:'center',width:'64px'}}>0</li>
                                    </ul>
                                    <ul style={{height:'25px',margin:"0px",width:'500px',fontSize:'10px'}}>
                                        <li style={{float:'left',textAlign:'center',width:'64px'}}>总任务</li>
                                        <li style={{float:'left',textAlign:'center',width:'64px'}}>未完成</li>
                                        <li style={{float:'left',textAlign:'center',width:'64px'}}>已完成</li>
                                        <li style={{float:'left',textAlign:'center',width:'64px'}}>已逾期</li>
                                        <li style={{float:'left',textAlign:'center',width:'64px'}}>已归档</li>
                                    </ul>
                                </div>
                            </div>
                            <div style={{height:'420px',backgroundColor:'#fff',marginTop:'10px'}}>
                                <div style={{height:'30px',fontSize: "13px",color: "#333",marginLeft:" 8px",lineHeight:'30px'}}>
                                    <h4>成员完成情况</h4>
                                </div>
                                <div style={{height:'390px'}}>
                                    <ul style={{height:'50px',display:'flex',border:'1px solid #e6e6e6',color:'#ccc'}}>
                                        <li style={{flex:'1',padding:'12px 0',backgroundColor: "#f5f5f5",textAlign:'left',paddingLeft:'8px'}}>姓名</li>
                                        <li style={{flex:'1',padding:'12px 0',backgroundColor: "#f5f5f5",borderLeft:'1px #e6e6e6 solid'}}>任务总数</li>
                                        <li style={{flex:'1',padding:'12px 0',backgroundColor: "#f5f5f5",borderLeft:'1px #e6e6e6 solid'}}>已完成数</li>
                                        <li style={{flex:'1',padding:'12px 0',backgroundColor: "#f5f5f5",borderLeft:'1px #e6e6e6 solid'}}>未完成数</li>
                                        <li style={{flex:'1',padding:'12px 0',backgroundColor: "#f5f5f5",borderLeft:'1px #e6e6e6 solid'}}>逾期数</li>
                                        <li style={{flex:'1',padding:'12px 0',backgroundColor: "#f5f5f5",borderLeft:'1px #e6e6e6 solid'}}>完成率</li>
                                    </ul>
                                    <div style={{height:'300px',position: "relative"}}>
                                        <div style={{width:'200px',height:'200px',position:'absolute',left:'50%',top:'50%',marginLeft:"-50px",marginTop:'-50px'}}>暂无数据</div>
                                    </div>
                                </div>
                                
                            </div>
                            
                        </div>
                    </TabPane>
                    <TabPane tab="归档任务" key="4">
                        <div style={{height:"600px",backgroundColor:"#fff"}}>
                            <div style={{width:'200px',height:'200px',position:'absolute',left:'50%',top:'50%',marginLeft:"-50px",marginTop:'-50px'}}>没有找到数据</div>
                        </div>
                    </TabPane>
                </Tabs>
           </div>
        </div>
    )
}

export default Gl_xm;