import axios from 'axios';
import React, { useState, useEffect } from 'react';
import * as echarts from 'echarts';


class Tong6 extends React.Component{

    componentDidMount() {
            var rq=[]
            var zq=[]
            var cjs=[]
            axios.post('http://crm.cimns.com/index.php/bi/customer/userCycle',{
                structure_id: 1,
                user_id: '',
                type:' year',
            }).then((data)=>{
                for (let t = 0; t < data.data.data.items.length; t++) {
                    rq.push(data.data.data.items[t].type) 
                    // console.log(data.data.data.items[t]);
                }
                for (let i = 0; i < data.data.data.items.length; i++) {
                    zq.push(data.data.data.items[i].cycle) 
                }
                for (let j = 0; j < data.data.data.items.length; j++) {
                    cjs.push(data.data.data.items[j].customer_num) 
                }

                var chartDom = document.getElementById('main');
                var myChart = echarts.init(chartDom);
                var option;

                option = {
                    title: {
                        text: '员工客户成交周期（根据合同下单时间和客户创建时间计算）',
                    },
                    xAxis: {
                        type: 'category',
                        data: rq,
                    },
                    yAxis: [
                        {
                            type: 'value',
                            scale: true,
                            name: '成交周期',
                            max: 1,
                            min: 0,
                            boundaryGap: [0.2, 0.2],
                            axisLabel: {
                                formatter: '{value} 天'
                            }
                        },{
                            type: 'value',
                            scale: true,
                            name: '成交客户数',
                            max: 1,
                            min: 0,
                            boundaryGap: [0.2, 0.2],
                            axisLabel: {
                                formatter: '{value} 个'
                            }
                        },
                    ],
                    series: [{
                        name:'成交周期',
                        data: zq,
                        type: 'bar'
                    },{
                        name:'成交客户数',
                        data: cjs,
                        type: 'bar'
                    }]
                };
                option && myChart.setOption(option);
         },(err)=>{
             console.log(err);
         })
    }    
        render() {
            return(
                <div id="main" style={
                   {width:1032,height:420}
                } ></div>
            )
        }
    }

export default Tong6