import Bg_gzt_qb from '../view/bg_gzt_qb'
import Bg_gzt_rz from '../view/bg_gzt_rz'
import Bg_gzt_sp from '../view/bg_gzt_sp'
import Bg_gzt_rw from '../view/bg_gzt_rw'
import Bg_gzt_rc from '../view/bg_gzt_rc'
import Bg_gzt_gg from '../view/bg_gzt_gg'
import { Tabs,Calendar } from 'antd';
import 'antd/dist/antd.css';
import '../view/css/bg_gzt.css'
const { TabPane } = Tabs;

function callback(key) {
  console.log(key);
}

let Bg_gzt = function(){

    return(
        <div className = 'zxzx'>
            <div className = 'gzt'>
                <Tabs defaultActiveKey="1" onChange={callback}>
                    <TabPane tab="全部" key="1">
                        <Bg_gzt_qb/>
                    </TabPane>
                    <TabPane tab="日志" key="2">
                        <Bg_gzt_rz/>
                    </TabPane>
                    <TabPane tab="审批" key="3">
                        <Bg_gzt_sp/>
                    </TabPane>
                    <TabPane tab="任务" key="4">
                        <Bg_gzt_rw/>
                    </TabPane>
                    <TabPane tab="日程" key="5">
                        <Bg_gzt_rc/>
                    </TabPane>
                    <TabPane tab="公告" key="6">
                        <Bg_gzt_gg/>
                    </TabPane>
                </Tabs>
                
            </div>
            <div className="clearfix zyyy">
              <div>
                <p style={{color:'#000'}}>任务</p>
                <img alt='0' src='data:image/png;base64,'/>
                <span>目前没有任务，快去<a>添加</a>吧！</span>
              </div>
              <div>
              <div className="site-calendar-demo-card" >
                <p style={{color:'#000'}}>
                  <span>日程</span>
                  <span><a>+创建</a></span>
                </p>
                <Calendar fullscreen={false}  />
              </div>
              </div>
            </div> 
        </div>
    )
}
export default Bg_gzt