import { Input, Select,Button } from 'antd';
import '../css/sy.css'
import Sy_31_tong from '../../components/sy_31_tong'
import Sy_31_biao from '../../components/sy_31_biao'
import { DatePicker, Space } from 'antd';
const { RangePicker } = DatePicker;
const { Option } = Select;
function Sy_31() {
    return(
        <div>
            <div className="sy-1-input">
            <Input.Group compact>
                    <Space direction="vertical" size={12} style={{width:130,height:34}}>
                        <DatePicker picker="year" bordered={true}   />
                    </Space>
                    <Select defaultValue="合同金额" style={{width:120,height:34,marginLeft:20}} >
                        <Option value="Option1">合同金额</Option>
                        <Option value="Option2">回款金额</Option>
                    </Select>
                    <Select defaultValue="Option1" style={{width:120,height:34,marginLeft:20}}>
                        <Option value="Option1">办公室</Option>
                        <Option value="Option2">打杂部</Option>
                    </Select>
                    <Select defaultValue="公司员工" style={{width:120,height:34,marginLeft:20}} >
                        <Option value="Option1">管理员</Option>
                        <Option value="Option2">管理员</Option>
                    </Select>
                    <Button type="primary" style={{marginLeft:20,marginTop:'auto'}}>搜索</Button>
                </Input.Group>
            </div>
            <div className="sy_31_tong" style={{marginTop:10}}>
                    <Sy_31_tong></Sy_31_tong>
            </div>
            <div className="sy_31_biao">
                <div className="s_31_biao">
                    <Sy_31_biao></Sy_31_biao>
                </div>
                   
            </div>
        </div>
    )
}

export default Sy_31