import React from 'react'
import {Switch,Route, Link} from 'react-router-dom'
import { Menu, Button } from 'antd';
import {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
    PieChartOutlined,
    DesktopOutlined,
    ContainerOutlined,
  } from '@ant-design/icons';
import './css/kh.css'

import Kh_1 from './kh_1'
import Kh_2 from './kh_2'
import Kh_3 from './kh_3'
import Kh_4 from './kh_4'
import Kh_5 from './kh_5'
import Kh_6 from './kh_6'
import Kh_7 from './kh_7'
import Kh_8 from './kh_8'
import Kh_9 from './kh_9'
import Kh_10 from './kh_10'
class Kh extends React.Component {
    state = {
        collapsed: false,
      };
    
      toggleCollapsed = () => {
        this.setState({
          collapsed: !this.state.collapsed,
        });
      };
      render() {
        return (
          <div className="l-khbox clearfix">
            <div  className = 'cd'>
              <Menu
                defaultSelectedKeys={['1']}
                defaultOpenKeys={['sub1']}
                mode="inline"
                theme="dark"
                inlineCollapsed={this.state.collapsed}
              >
                <div className="dj"><p>快速创建</p></div>
                  
                <Menu.Item key="1" icon={<PieChartOutlined />}>
                  <Link to="/App/box/kh">
                  仪表盘
                  </Link>
                </Menu.Item>
                <Menu.Item key="2" icon={<DesktopOutlined />}>
                <Link to="/App/box/kh/kh_2">
                  待办事项
                </Link>
                </Menu.Item>
                <Menu.Item key="3" icon={<ContainerOutlined />}>
                  <Link to="/App/box/kh/kh_3">
                  线索
                  </Link>
                </Menu.Item>
                <Menu.Item key="4" icon={<ContainerOutlined />}>
                  <Link to="/App/box/kh/kh_4">
                    客户
                  </Link>
                </Menu.Item>
                <Menu.Item key="5" icon={<ContainerOutlined />}>
                  <Link to="/App/box/kh/kh_5">
                    联系人
                  </Link>
                </Menu.Item>
                <Menu.Item key="6" icon={<ContainerOutlined />}>
                  <Link to="/App/box/kh/kh_6">
                    公海
                  </Link>
                </Menu.Item>
                <Menu.Item key="7" icon={<ContainerOutlined />}>
                  <Link to="/App/box/kh/kh_7">
                    商机
                  </Link>
                </Menu.Item>
                <Menu.Item key="8" icon={<ContainerOutlined />}>
                  <Link to="/App/box/kh/kh_8">
                    合同
                  </Link>
                </Menu.Item>
                <Menu.Item key="9" icon={<ContainerOutlined />}>
                  <Link to="/App/box/kh/kh_9">
                    回款
                  </Link>
                  
                </Menu.Item>
                <Menu.Item key="10" icon={<ContainerOutlined />}>
                  <Link to="/App/box/kh/kh_10">
                    产品
                  </Link>
                  
                </Menu.Item>
                <div>
                  <Button type="primary" onClick={this.toggleCollapsed} style={{ marginBottom: 16 }}>
                      {React.createElement(this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined)}
                  </Button>
                </div>
               
              </Menu>
            </div>
            <div className="ly">
                <Switch>
                  <Route path="/App/box/kh" component={Kh_1} exact/>
                  <Route path="/App/box/kh/kh_2" component={Kh_2}/>
                  <Route path="/App/box/kh/kh_3" component={Kh_3}/>
                  <Route path="/App/box/kh/kh_4" component={Kh_4}/>
                  <Route path="/App/box/kh/kh_5" component={Kh_5}/>
                  <Route path="/App/box/kh/kh_6" component={Kh_6}/>
                  <Route path="/App/box/kh/kh_7" component={Kh_7}/>
                  <Route path="/App/box/kh/kh_8" component={Kh_8}/>
                  <Route path="/App/box/kh/kh_9" component={Kh_9}/>
                  <Route path="/App/box/kh/kh_10" component={Kh_10}/>
                </Switch>
              </div>
          </div>
        );
      }
}
export default Kh