import { Table } from 'antd';
import '../view/css/biao.css'
import axios from 'axios'
import React, { useState, useEffect } from 'react';
const columns = [
    {
      title: '员工姓名',
      dataIndex: 'name',
      width: 150,
    },
    {
      title: '新增客户数',
      dataIndex: 'aga',
      width: 150,
    },
    {
        title: '成交客户数',
        dataIndex: 'agb',
        width: 150,
      },
      {
        title: '客户成交率(%)',
        dataIndex: 'agc',
        width: 150,
      },
      {
        title: '合同总金额',
        dataIndex: 'agd',
        width: 150,
      },
      {
        title: '回款金额',
        dataIndex: 'age',
        width: 150,
      },
      {
        title: '未回款金额',
        dataIndex: 'agf',
        width: 150,
      },
    {
      title: '回款完成率(%)',
      dataIndex: 'address',
    },
  ];
  
  

  function Biao() {
    const [arr,setarr] = useState([])
    useEffect(() => {
      axios.post('http://crm.cimns.com/index.php/bi/customer/statistics',{
        structure_id: 1,
        user_id: '',
        type: 'year'
      }).then((data)=> {
        setarr(data.data.data)
      })
    },[])

    const data = [];
    for (let i = 0; i < arr.length; i++) { 
      data.push({
        key: i,
        name: arr[i].realname,
        age: arr[i].contract_money,
        address:arr[i].deal_receivables_rate,
        aga:arr[i].customer_num,
        agb:arr[i].deal_customer_num,
        agd:arr[i].receivables_money,
        agc:arr[i].deal_customer_rate,
        agf:arr[i].un_receivables_money

      });
    }
      return(
        <div>
            <Table columns={columns} dataSource={data}  scroll={{ y: 370 }} pagination={false} bordered='true'/>
        </div>
      )
  }

export default Biao