import React, {Component} from "react";
import * as echarts from 'echarts';

class Echarts extends Component {
    
    componentDidMount() {
        var chartDom = document.getElementById('main')
        var myChart = echarts.init(chartDom);
        var option;
        option = {
            series: [{
                type: 'gauge',
                axisLine: {
                    lineStyle: {
                        width: 20,
                        color: [
                            [0.3, '#ff7474'],
                            [0.7, '#fecd51'],
                            [1, '#48e78d']
                        ]
                    }
                },
                pointer: {
                    itemStyle: {
                        color: '#5f879d'
                    }
                },
                axisTick: {
                    distance: -30,
                    length: 6,
                    lineStyle: {
                        color: '#fff',
                        width: 2
                    }
                },
                splitLine: {
                    distance: -30,
                    length: 15,
                    lineStyle: {
                        color: '#fff',
                        width: 0
                    }
                },
                axisLabel: {
                    color: 'auto',
                    distance: 40,
                    fontSize: 10
                },
                detail: {
                    fontSize:14,
                    valueAnimation: true,
                    formatter: '{value} %',
                    color: '#5f879d'
                },
                data: [{
                    value: 0
                }]
            }]
        
        };
        option && myChart.setOption(option);
    }
    render() { 
        return(
            <div>
                <div id = "main"style = {{width: 220,height: 220,}} ></div>
            </div>
        )
    } 
}

export default Echarts;