import { Table } from 'antd';
import '../view/css/biao.css'
import axios from 'axios'
import React, { useState, useEffect } from 'react';

  function Sy_15_biao() {
    const [arr,setarr] = useState([])
    useEffect(() => {
      axios.post('http://crm.cimns.com/index.php/bi/customer/portrait',{
        structure_id: 1,
        user_id: '',
        type: 'year',
        type_analyse: 'industry'
      }).then((data)=> {
        setarr(data.data.data)
      })
    },[])
    const columns = [
        {
          title: '客户行业',
          width: 180,
          dataIndex: 'name',
          key: 'name',
          fixed: 'left',
          align:'center'
        },
        { title: 'IT/通信/电子/互联网', dataIndex: 'address', key: '1',align:'center' },
        { title: '金融业', dataIndex: 'address2', key: '2',align:'center' },
        { title: '房地产', dataIndex: 'address3', key: '3',align:'center' },
        { title: '商业服务', dataIndex: 'address4', key: '4',align:'center' },
        { title: '贸易', dataIndex: 'address5', key: '5',align:'center' },
        { title: '生产', dataIndex: 'address6', key: '6',align:'center' },
        { title: '运输/物流', dataIndex: 'address7', key: '7',align:'center' },
        { title: '服务业', dataIndex: 'address8', key: '8',align:'center' },
        { title: '文化传媒', dataIndex: 'address9', key: '9',align:'center' },
        { title: '政府', dataIndex: 'address10', key: '10',align:'center' },
        { title: '其他', dataIndex: 'address11', key: '11',align:'center' },
      ];
      let data=[]
      if (arr.length === 0) {
        
      }else{
        data = [
          {
            key: '1',
            name: '所有客户(个)',
            address: arr[0].allCustomer,
            address2:arr[1].allCustomer,
            address3:arr[2].allCustomer,
            address4:arr[3].allCustomer,
            address5:arr[4].allCustomer,
            address6:arr[5].allCustomer,
            address7:arr[6].allCustomer,
            address8:arr[7].allCustomer,
            address9:arr[8].allCustomer,
            address10:arr[9].allCustomer,
            address11:arr[10].allCustomer,
          },
          {
            key: '2',
            name: '成交客户(个)',
            address: arr[0].dealCustomer,
            address2:arr[1].dealCustomer,
            address3:arr[2].dealCustomer,
            address4:arr[3].dealCustomer,
            address5:arr[4].dealCustomer,
            address6:arr[5].dealCustomer,
            address7:arr[6].dealCustomer,
            address8:arr[7].dealCustomer,
            address9:arr[8].dealCustomer,
            address10:arr[9].dealCustomer,
            address11:arr[10].dealCustomer,
          },
        ];
      }
      
      return(
        <div>
            <Table columns={columns} dataSource={data} scroll={{ x: 1300 }} pagination={false} bordered='true' />
        </div>
      )
  }

export default Sy_15_biao