import React, { } from 'react';
import axios from 'axios';
import * as echarts from 'echarts';


class Tong extends React.Component{
    
    componentDidMount() {
        var rq=[]
        var jy=[]
        var jycg=[]
        axios.post('http://crm.cimns.com/index.php/bi/customer/total',{
            structure_id: 1,
            user_id: '',
            type:' year',
        }).then((data)=>{
            for (let t = 0; t < data.data.data.length; t++) {
                rq.push(data.data.data[t].type)
            }
            for (let i = 0; i < data.data.data.length; i++) {
                jy.push(data.data.data[i].customer_num)
            }
            for (let j = 0; j < data.data.data.length; j++) {
                jycg.push(data.data.data[j].deal_customer_num)
            }
            var chartDom = document.getElementById('main');
            var myChart = echarts.init(chartDom);
            var option;

            option = {
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {            
                        type: 'shadow'        
                    }
                },
                xAxis: {
                    type: 'category',
                    data: rq,
                   
                },
                yAxis: [
                    {
                        type: 'value',
                        scale: true,
                        name: '新增客户数',
                        max: 2,
                        min: 0,
                        boundaryGap: [0.5,],
                        axisLabel: {
                            formatter: '{value} 个'
                        }
                    }
                ],
                series: [{
                    name:'全部客户',
                    data: jy,
                    type: 'bar'
                },{
                    name:'成交客户',
                    // data: jycg,
                    type: 'bar'
                }
            ]
            };
            option && myChart.setOption(option);
        },(err)=>{
            console.log(err)
        })     
        
        }
        
        render() {
            return(
                <div id="main" style={
                   {width:1032,height:420}
                } ></div>
            )
        }
    }

export default Tong