import { Route, Switch } from 'react-router'
import { Link } from 'react-router-dom'
import '../css/sy.css'
import Sy_6_1 from './sy_6_1'
import Sy_6_2 from './sy_6_2'
import Sy_6_3 from './sy_6_3'

function Sy_6() {
    return(
        <div>
            <div className="sy6-title">
                <ul>
                    <li><Link to="/App/box/sy/sy_6/" style={{color:'black'}}>员工客户成交周期</Link> </li>
                    <li><Link to="/App/box/sy/sy_6/sy_6_2" style={{color:'black'}}>地区成交周期</Link> </li>
                    <li><Link to="/App/box/sy/sy_6/sy_6_3" style={{color:'black'}}>产品成交周期</Link> </li>
                </ul>
            </div>
            <div className="sy6-text">
                <Switch>
                    <Route path='/App/box/sy/sy_6/' component={Sy_6_1} exact />
                    <Route path="/App/box/sy/sy_6/sy_6_2" component={Sy_6_2} />
                    <Route path="/App/box/sy/sy_6/sy_6_3" component={Sy_6_3} />
                </Switch> 
            </div>
        </div>
    )
}

export default Sy_6