import { Table } from 'antd';
import '../view/css/biao.css'
import React, { useState, useEffect } from 'react';
import axios from 'axios'
  function Sy_18_biao() {
    const columns = [
        {title: '产品分类', dataIndex: 'cpfl', key: 'name', align:'center'},
        { title: '产品名称', dataIndex: 'cpmc', key: '1',align:'center' },
        { title: '合同编号', dataIndex: 'htbh', key: '2',align:'center' },
        { title: '负责人', dataIndex: 'fzr', key: '3',align:'center' },
        { title: '客户名称', dataIndex: 'khmc', key: '4',align:'center' },
        { title: '销售单价', dataIndex: 'jg', key: '5',align:'center' },
        { title: '数量', dataIndex: 'num', key: '4',align:'center' },
        { title: '订单产品小计', dataIndex: 'xj', key: '5',align:'center' },
      ];
      
      const [arr,setarr] = useState([])
      useEffect(() => {
        axios.post('http://crm.cimns.com/index.php/bi/product/statistics',{
          structure_id: 1,
          user_id: "",
          type: "year"
        }).then((data)=> {
          setarr(data.data.data)
          console.log(data.data.data);
        })
      },[])
      const data = [];
      for(let i = 0; i < arr.length;i++) {
        data.push({
          key : i,
          cpfl:arr[i].category_id_info,
          cpmc:arr[i].product_name,
          htbh:arr[i].contract_name,
          fzr:arr[i].realname,
          khmc:arr[i].customer_id_info.name,
          jg:arr[i].price,
          num:arr[i].num,
          xj:arr[i].subtotal,
         });
    }
      return(
        <div style={{height:570}}>
            <Table columns={columns} dataSource={data} pagination={false} bordered='true' style={{height:570}} />
        </div>
      )
  }

export default Sy_18_biao 