import React, { useState, useEffect} from 'react';
import axios from 'axios'
import {Input, Space,Select, Modal, Button,Pagination,DatePicker} from 'antd';
import './css/kh_3.css'


const { Search } = Input;

  const onSearch = value => console.log(value);
  const { Option } = Select;

  function handleChange(value) {
    console.log(`selected ${value}`);
  }
  function onChange(date, dateString) {
    console.log(date, dateString);
  }
function Kh_7(){
    const [visible, setVisible] = React.useState(false);
    const [confirmLoading, setConfirmLoading] = React.useState(false);
    const [modalText, setModalText] = React.useState('Content of lie modal');
  
    const showModal = () => {
      setVisible(true);
    };
  
    const handleOk = () => {
      setModalText('lie modal will be closed after two seconds');
      setConfirmLoading(true);
      setTimeout(() => {
        setVisible(false);
        setConfirmLoading(false);
      }, 2000);
    };
  
    const handleCancel = () => {
      console.log('Clicked cancel button');
      setVisible(false);
    };
  
    const [data, setData] = useState([]);
    useEffect(() => {
      const fetchData = async () => {
        const result = await axios.post(
            'http://crm.cimns.com/index.php/crm/business/index',{
              page: 1,
              limit: 15,
              search: '',
            }
          )
        console.log(result.data);
        setData(result.data.data.list);
      };
      fetchData(); 
    }, []);
    console.log(data)
   
    return(
        <div className="kh_3">
            <div className="kh_3-header clearfix">
                <div className="kh_3-header-title">
                    <h2>商机管理</h2>
                </div>
                <div className="kh_3-header-ss">
                    <Space direction="vertical">
                        <Search placeholder="input search text" onSearch={onSearch} style={{ width: 200 }} />
                    </Space>
                </div>
                <div className="kh_3-header-cj clearfix">
                    <div className="kh_3-header-cj-1">
                    <>
                            <Button type="primary" onClick={showModal}>
                                新建商机
                            </Button>
                            <Modal
                                title="新建商机"
                                visible={visible}
                                onOk={handleOk}
                                confirmLoading={confirmLoading}
                                onCancel={handleCancel}
                            >
                                <div style={{height:'600px'}}>
                                    <div style={{height:'360px'}}>
                                        <h4>基本信息</h4>
                                        <div style={{float:'left',width:'309px',height:'77px'}}>
                                            <h6 style={{margin:'5px 10px'}}>商机名称</h6>
                                            <Input style={{width:'284px',margin:'0 10px'}}/>  
                                        </div>
                                        <div style={{float:'left',width:'309px',height:'77px'}}>
                                            <h6 style={{margin:'5px 10px'}}>客户名称</h6>
                                            <Input style={{width:'284px',margin:'0 10px'}}/>  
                                        </div>
                                        <div style={{float:'left',width:'309px',height:'77px'}}>
                                            <h6 style={{margin:'5px 10px'}}>商机状态组</h6>
                                            <>
                                                <Select defaultValue="请选择" style={{ width: 284 }} onChange={handleChange}>
                                                    <Option value="1">默认</Option>
                                                    
                                                </Select>
                                            </>
                                        </div>
                                        <div style={{float:'left',width:'309px',height:'77px'}}>
                                            <h6 style={{margin:'5px 10px'}}>商机阶段</h6>
                                            <>
                                                <Select defaultValue="请选择" style={{ width: 284 }} onChange={handleChange}>
                                                    <Option value="1">赢单</Option>
                                                    <Option value="2">输单</Option>
                                                    <Option value="3">无效</Option>
                                                </Select>
                                            </>
                                        </div>
                                        <div style={{float:'left',width:'309px',height:'77px'}}>
                                            <h6 style={{margin:'5px 10px'}}>商机金额(元)</h6>
                                            <Input style={{width:'284px',margin:'0 10px'}}/>  
                                        </div>
                                        <div style={{float:'left',width:'309px',height:'77px'}}>
                                            <h6 style={{margin:'5px 10px'}}>预计成交日期</h6>
                                            <Space direction="vertical" style={{margin:'0 0 0 10px'}}>
                                                <DatePicker onChange={onChange} />
                                            </Space>  
                                        </div>
                                        <div style={{float:'left',width:'309px',height:'77px'}}>
                                            <h6 style={{margin:'5px 10px'}}>备注</h6>
                                            <Input style={{width:'284px',height:'70px',margin:'0 10px'}}/>  
                                        </div>
                                    </div>
                                    <div style={{height:'120px'}}>
                                            <h6>产品</h6>
                                            <div style={{width:'60px',height:'30px',background:"#39A5FF",float:"right",color:'#fff',lineHeight:'30px',textAlign:'center'}}>
                                                添加产品
                                            </div>
                                            <div style={{height:'150px',width:"660px", overflow:'hidden',textOverflow:"ellipsis",whiteSpace:"nowrap"}}>
                                                <div style={{height:'70px',width:'800px'}}>
                                                    <ul style={{height:'70px',width:'800px',borderBottom:'1px solid #ccc'}}>
                                                        <li style={{padding:'15px',float:'left'}}>产品名称</li>
                                                        <li style={{padding:'15px',float:'left'}}>产品类型</li>
                                                        <li style={{padding:'15px',float:'left'}}>单位</li>
                                                        <li style={{padding:'15px',float:'left'}}>标准价格</li>
                                                        <li style={{padding:'15px',float:'left'}}>售价</li>
                                                        <li style={{padding:'15px',float:'left'}}>数量</li>
                                                        <li style={{padding:'15px',float:'left'}}>折扣(%)</li>
                                                        <li style={{padding:'15px',float:'left'}}>合计</li>
                                                        <li style={{padding:'15px',float:'left'}}>操作</li>
                                                    </ul>
                                                </div>
                                                <div style={{height:'50px'}}> 
                                                    <span style={{display:'block',textAlign:'center',lineHeight:'50px'}}>暂无数据</span>
                                                </div>
                                            </div>
                                            <div style={{height:'60px'}}>
                                                <div style={{float:'left',width:'309px',height:'77px'}}>
                                                    <span style={{margin:'5px 10px'}}>整单折扣(%)</span>
                                                    <Input style={{width:'60px',height:'30px',margin:'0 10px'}}/>  
                                                </div>
                                                <div style={{float:'left',width:'309px',height:'77px'}}>
                                                    <span style={{margin:'5px 10px'}}>已选中产品:0种 总金额:</span>
                                                    <Input style={{width:'60px',height:'30px',margin:'0 10px'}}/>  
                                                    <span>元</span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                            </Modal>
                        </>
                    </div>
                    <div className="kh_3-header-cj-2">
                        <>
                            <Select defaultValue="jack" style={{ width: 70 }} onChange={handleChange}>
                                <Option value="jack">更多</Option>
                                <Option value="lucy">导出</Option>
                            </Select>
                        </>
                    </div>
                </div>
            </div>
            <div className="kh_3-con-top">
                    <div className="kh_3-con-top-1">场景:</div>
                    <div className="kh_3-con-top-2">
                    <>
                        <Select defaultValue="lucy" style={{ width: 120 }} bordered={false}>
                            <Option value="jack">我负责的商机</Option>
                            <Option value="lucy">全部商机</Option>
                            <Option value="Yiminghe">下属负责的商机</Option>
                            <Option value="cxe">我参与的商机</Option>
                        </Select>
                    </>
                    </div>
                    <div className="kh_3-con-top-3">
                    <>
                        <Button type="primary" onClick={showModal}>
                            高级筛选
                        </Button>
                        <Modal
                            // title="Title"
                            // visible={visible}
                            // onOk={handleOk}
                            // confirmLoading={confirmLoading}
                            // onCancel={handleCancel}
                        >
                        <p>{modalText}</p>
                        </Modal>
                    </>
                    </div> 
                </div>
            <div className="kh_3-con">
                <div className="kh_3-con-con">
                    <div className="kh_3-con-con-1">
                        <ul>
                                <li>商机名称</li>
                                <li>客户名称</li>
                                <li>商机状态组</li>
                                <li>商机阶段</li>
                                <li>商机金额</li>
                                <li>预计成交日期</li>
                                <li>备注</li>
                                <li>创建人</li>
                                <li>更新时间</li>
                                <li>创建时间</li>
                                <li>负责人</li>
                               
                            </ul>
                    </div>
                    <div className="kh_3-con-con-2">
                       
                        {data.map((item)=>( 
                            <ul key={item.create_user_id}>
                                    <li>{item.name}</li>
                                      <li> {item.customer_name}</li>
                                      <li> {item.type_id_info}</li>
                                      <li>{item.status_id_info}</li>
                                      <li> {item.money}</li>
                                      <li> {item.deal_date}</li>
                                      <li> {item.remark}</li>
                                      <li> {item.create_user_id_info.realname}</li>
                                      <li>  {item.update_time}</li>
                                      <li> {item.update_time}</li>
                                      <li> {item.owner_user_id_info.realname}</li>
                                </ul>
                                ))}  
                          
                       
                    </div>
                </div>
            </div>
            <div className="kh_3-footer">
                    <>
                        <Pagination size="small" total={50} pagination={false} showSizeChanger showQuickJumper />
                    </>
                </div>
        </div>
    )
}
export default Kh_7