import React, {} from 'react';
import axios from 'axios';
import * as echarts from 'echarts';



class Sy_4_bing extends React.Component{

    componentDidMount() {
        var sj=[]
        var bl=[]
        axios.post('http://crm.cimns.com/index.php/bi/customer/conversion',{
            structure_id: 1,
            user_id: '',
            type:' year',
        }).then((data)=>{
            console.log(data);
            var chartDom = document.getElementById('main');
            var myChart = echarts.init(chartDom);
            var option;
            for (let t = 0; t < data.data.data.length; t++) {
                sj.push(data.data.data[t].type)
            }
            for (let i = 0; i < data.data.data.length; i++) {
                bl.push(data.data.data[i].proportion)
                
            }
            option = {
                tooltip: {
                    trigger: 'item'
                },
                legend: {
                    orient: 'vertical',
                    // left: 'left',
                    bottom:'bottom'
                },
                series: [
                    {
                        name: '访问来源',
                        type: 'pie',
                        radius: '50%',
                        data: [
                            {value: bl[0], name: sj[0]},
                            {value: bl[1], name: sj[1]},
                            {value: bl[2], name: sj[2]},
                            {value: bl[3], name: sj[3]},
                            {value: bl[4], name: sj[4]},
                            {value: bl[5], name: sj[5]},
                            {value: bl[6], name: sj[6]},
                            {value: bl[7], name: sj[7]},
                            {value: bl[8], name: sj[8]},
                            {value: bl[9], name: sj[9]},
                            {value: bl[10], name: sj[10]},
                            {value: bl[11], name: sj[11]}
                        ],
                        emphasis: {
                            itemStyle: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }
                ]
            };
                
            option && myChart.setOption(option);
        })
    }
        
        render() {
            return(
                <div id="main" style={{width:1032,height:380,}}></div>
            )
        }
}
export default Sy_4_bing